//              <Search docID={demographic.id} />

function Search(props) {
  const [showResults, setShowResults] = React.useState(false);
  const [deleteDocument, setDeleteDocument] = React.useState('None');

  const onClick = () => setShowResults(true);

  //SK Set the docID passed from the link.
  //setDocumentID({ props.docID });
  //console.log('demographicview:props:' + JSON.stringify(props));
  console.log('demographicview:props: docID:' + props.docID);

  return (
    <div>
      <input type="submit" value="Delete Demographic" onClick={onClick} />
      {showResults ? (
        <label>
          <Form.Text>Delete?</Form.Text>
          <select
            value={deleteDocument}
            onChange={({ target }) => setDeleteDocument(target.value)}
          >
            {' '}
            <option value="Yes">Yes</option>
            <option value="No">No</option>
            <option value="None">None</option>
          </select>
          <Form.Submit
            deleteDocument={true}
            type="submit"
            data-testid="delete-activity"
          >
            Save Demographic Delete
          </Form.Submit>
        </label>
      ) : null}
    </div>
  );
}

export function DemographicViewPage() {
  const { firebase } = useContext(FirebaseContext);

  const [error, setError] = useState('');
  const [demographics, setDemographics] = useState([]);
  // SK
  const [userLocal, setUserLocal] = useState(
    JSON.parse(localStorage.getItem('authUser'))
  );

  useEffect(() => {
    db.collection('demographics')
      .where('createdBy', '==', userLocal.uid)
      .where('deleteDocument', '==', false)
      .get()
      .then((response) => {
        const fetchedDemographics = [];
        response.docs.forEach((document) => {
          const fetchedDemographic = {
            id: document.id,
            ...document.data(),
          };
          fetchedDemographics.push(fetchedDemographic);
        });
        setDemographics(fetchedDemographics);
      })
      .catch((error) => {
        setError(error);
      });
  }, []);

  return (
    <>
      <Form>
        <Form.SubTitle>View Demographics</Form.SubTitle>
        {error && <Form.Error>{error}</Form.Error>}
        <Form.Table>
          <Form.Thead>
            <Form.Tr>
              <Form.Th>Type</Form.Th>
              <Form.Th>Full Name</Form.Th>
              <Form.Th>Birthday</Form.Th>
              <Form.Th>Birthplace</Form.Th>
              <Form.Th>Gender</Form.Th>
              <Form.Th>Ethnicity</Form.Th>
              <Form.Th>Hispanic or Latino</Form.Th>
              <Form.Th>Religion</Form.Th>
              <Form.Th>Military</Form.Th>
              <Form.Th>Citizenship Status</Form.Th>
              <Form.Th>Description</Form.Th>
              <Form.Th>Updates</Form.Th>
              <Form.Th>Link</Form.Th>
            </Form.Tr>
          </Form.Thead>
          <Form.Tbody>
            {error ? <p>Ops, there is an error :(</p> : null}
            {demographics.map((demographic) => (
              <Form.Tr key={demographics.id}>
                <Form.Td> {demographic.demographicType}</Form.Td>
                <Form.Td> {demographic.demographicName}</Form.Td>
                <Form.Td> {demographic.birthday}</Form.Td>
                <Form.Td> {demographic.birthplace}</Form.Td>
                <Form.Td> {demographic.gender}</Form.Td>
                <Form.Td> {demographic.ethnicity}</Form.Td>
                <Form.Td> {demographic.hispanicLatino}</Form.Td>
                <Form.Td> {demographic.religion}</Form.Td>
                <Form.Td> {demographic.military}</Form.Td>
                <Form.Td> {demographic.citizenshipStatus}</Form.Td>
                <Form.Td> {demographic.description}</Form.Td>
                <Form.Td> {demographic.updates}</Form.Td>
                <Form.Td> {demographic.demographicLinnk}</Form.Td>
              </Form.Tr>
            ))}
          </Form.Tbody>
        </Form.Table>
        <Form.Link to="/demographicview">View Demographics</Form.Link>
        <Form.Link to="/demographic">Add Demographics</Form.Link>
        <Form.SubTitle>Demographic Details</Form.SubTitle>
        <Form.Table>
          {demographics.map((demographic) => (
            <Form.Tr key={demographic.id}>
              <Form.Tr>
                <Form.Th>Field</Form.Th>
                <Form.Th>Value</Form.Th>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Demographics Type</Form.Td>
                <Form.Td>{demographic.demographicType}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Demographic Name</Form.Td>
                <Form.Td>{demographic.demographicName}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Birthday</Form.Td>
                <Form.Td>{demographic.birthday}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Birthplace</Form.Td>
                <Form.Td>{demographic.birthplace}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Gender</Form.Td>
                <Form.Td>{demographic.gender}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Ethnicity</Form.Td>
                <Form.Td>{demographic.ethnicity}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Hispanic Latino</Form.Td>
                <Form.Td>{demographic.hispanicLatino}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Religion</Form.Td>
                <Form.Td>{demographic.religion}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Military</Form.Td>
                <Form.Td>{demographic.military}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Citizenship Status</Form.Td>
                <Form.Td>{demographic.citizenshipStatus}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Description</Form.Td>
                <Form.Td>{demographic.description}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Demographic Link</Form.Td>
                <Form.DriveLink
                  driveLink={demographic.demographicLink}
                  drive={drive}
                />
              </Form.Tr>

              <Form.Text>
                <CommentsView
                  reviewee={userLocal.uid}
                  docID={demographic.id}
                  docType="demographic"
                />
              </Form.Text>

              <Form.Text>
                <DemographicUpdate
                  createdBy={userLocal.uid}
                  docID={demographic.id}
                  docObject={demographic}
                />
              </Form.Text>
            </Form.Tr>
          ))}
        </Form.Table>

        <Form.Link to="/demographic">Add Demographic</Form.Link>
        <Form.Link to="/mwnu-stories-view">Return to Stories</Form.Link>
        <Form.Link to="/browse">Return to Browse</Form.Link>

        <Form.TextSmall>You are so awesome!</Form.TextSmall>
      </Form>
    </>
  );
}
