import React, { useState, useContext } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//SK
import { db } from '../context/firebase';
import useMetadata from '../constants/metadata';

export default function Activity() {
  const { firebase } = useContext(FirebaseContext);


  //SK Need to have activityTypes as var and double-quotes around "activitytypes".
  //var activityTypes  = useContent("activitytypes");
  var activityTypes = useMetadata("activityType");
  var activityScopes = useMetadata("activityScope");
  var activityStatuses = useMetadata("status");
  var activityReviews = useMetadata("review");
  var activityReviewers = useMetadata("reviewer");

//    console.log('activityTypes:' + JSON.stringify(activityTypes));

//{activityTypes.map((activityTypeMeta) => (<option value={activityTypeMeta.code}>{activityTypeMeta.type}</option>))}

  const [error] = useState('');
  //SK
  var user = firebase.auth().currentUser;
  const [loader, setLoader] = useState(false);

  const [activityType, setActivityType] = useState('None');
  const [activityName, setActivityName] = useState('');
  const [description, setDescription] = useState('');
  const [activityScope, setActivityScope] = useState('None');

  const [startDate, setStartDate] = useState('');

  const [status, setStatus] = useState('None');
  const [review, setReview] = useState('None');

  const [isActivity, setIsActivity] = useState(false);
  const [isHonor, setIsHonor] = useState(false);
  const [isAdditional, setIsAdditional] = useState(false);

  const [activityLink, setActivityLink] = useState('');
  const [reviewers, setReviewers] = useState('');

  const [deleteDocument, setDeleteDocument] = useState(false);

  //SK Activate Save button only if activityName is populated.
  const isInvalid = activityName === '';

  const handleActivity = (event) => {
    event.preventDefault();

    //        createdAt: firebase.firestore.FieldValue.serverTimestamp(),

    db.collection('activity')
      .add({
        activityType: activityType,
        activityName: activityName,
        description: description,
        activityScope: activityScope,
        startDate: startDate,
        activityLink: activityLink,
        status: status,
        review: review,
        isActivity: isActivity,
        isHonor: isHonor,
        isAdditional: isAdditional,
        createdAt: new Date(),
        createdBy: user.uid,
        reviewers: [reviewers],
        deleteDocument: deleteDocument,
        users: [
          {
            userId: user.uid,
          },
        ],
      })
      .then(() => {
        setLoader(false);
        alert('Your activity has been saved!');
      })
      .catch((error) => {
        alert(error.message);
        setLoader(false);
      });
    setActivityType('None');
    setActivityName('');
    setDescription('');
    setActivityScope('None');

    setStatus('None');
    setReview('None');

    setIsActivity(true);
    setIsHonor(false);
    setIsAdditional(false);

    setReviewers('');

    setStartDate('');
    setActivityLink('');

    setDeleteDocument(false);
  };

  return (
    <>
      <Form>
        <Form.Title>Add Activity</Form.Title>
        {error && <Form.Error>{error}</Form.Error>}

        <Form.Base onSubmit={handleActivity} method="POST">
          <label>
            <Form.Text>Choose Activity Type:</Form.Text>
            <select
              value={activityType}
              data-lpignore="true"
              onChange={({ target }) => setActivityType(target.value)}
            >
{' '}
{activityTypes.map(type => (<option value={type.code}>{type.type}</option>))}
             

            </select>
          </label>

          <Form.Input
            placeholder="Activity Name (Required)"
            value={activityName}
            type="search"
            data-lpignore="true"
            onChange={({ target }) => setActivityName(target.value)}
          />
          <Form.InputLong
            placeholder="Description"
            type="search"
            value={description}
            data-lpignore="true"
            onChange={({ target }) => setDescription(target.value)}
          />

          <Form.Input
            placeholder="Start Date"
            type="date"
            value={startDate}
            data-lpignore="true"
            onChange={({ target }) => setStartDate(target.value)}
          />

          <label>
            <Form.Text>Choose Scope:</Form.Text>
            <select
              value={activityScope}
              data-lpignore="true"
              onChange={({ target }) => setActivityScope(target.value)}
            >
              {' '}
              {activityScopes.map(type => (<option value={type.code}>{type.type}</option>))}

            </select>
          </label>

          <Form.Table>
            <Form.Tr>
              <Form.Td>
                <label>
                  <Form.Text>Is Activity:</Form.Text>
                  <Form.Input
                    placeholder="Is Activity"
                    type="checkbox"
                    checked={isActivity}
                    onChange={({ target }) => setIsActivity(target.value)}
                  />
                </label>
              </Form.Td>
              <Form.Td>
                <label>
                  <Form.Text>Is Honor:</Form.Text>
                  <Form.Input
                    placeholder="Is Honor"
                    type="checkbox"
                    checked={isHonor}
                    onChange={({ target }) => setIsHonor(target.value)}
                  />
                </label>
              </Form.Td>
              <Form.Td>
                <label>
                  <Form.Text>Is Additional Info:</Form.Text>
                  <Form.Input
                    placeholder="Is Additional Info"
                    type="checkbox"
                    checked={isAdditional}
                    onChange={({ target }) => setIsAdditional(target.value)}
                  />
                </label>
              </Form.Td>
            </Form.Tr>
          </Form.Table>

          <Form.Table>
            <Form.Tr>
              <Form.Td>
                <label>
                  <Form.Text>Choose Status:</Form.Text>
                  <select
                    value={status}
                    onChange={({ target }) => setStatus(target.value)}
                  >
                    {' '}
                    {activityStatuses.map(type => (<option value={type.code}>{type.type}</option>))}
                  </select>
                </label>
              </Form.Td>
              <Form.Td>
                <label>
                  <Form.Text>Choose Review:</Form.Text>
                  <select
                    value={review}
                    onChange={({ target }) => setReview(target.value)}
                  >
                    {' '}
                    {activityReviews.map(type => (<option value={type.code}>{type.type}</option>))}
                  </select>
                </label>
              </Form.Td>
            </Form.Tr>
          </Form.Table>

          <Form.Input
            placeholder="Link: (example:) https://drive.google.com/drive/my-drive"
            value={activityLink}
            data-lpignore="true"
            onChange={({ target }) => setActivityLink(target.value)}
          />

          <label>
            <Form.SubTitle>Reviewer</Form.SubTitle>
            <select
              value={reviewers}
              onChange={({ target }) => setReviewers(target.value)}
            >
              {''}
              {activityReviewers.map(reviewer => (<option value={reviewer.uid}>{reviewer.profileName}</option>))}

              
            </select>
          </label>

          <Form.Submit disabled={isInvalid} type="submit" data-testid="sign-up">
            Save Activity
          </Form.Submit>
        </Form.Base>

        <Form.Text>
          Want to see Activites?{' '}
          <Form.Link to="/activityview">View Activities</Form.Link>
        </Form.Text>
        <Form.Link to="/browse">Return to Browse</Form.Link>
        <Form.TextSmall>You are so awesome!</Form.TextSmall>
      </Form>
    </>
  );
}
