import React, { useState, useContext } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//SK
import { db } from '../context/firebase';

export default function Honor() {
  const { firebase } = useContext(FirebaseContext);

  const [error] = useState('');
  //SK
  var user = firebase.auth().currentUser;
  const [ setLoader] = useState(false);

  const [honorType, setHonorType] = useState('');
  const [honorName, setHonorName] = useState('');
  const [honorScope, setHonorScope] = useState('None');

  const [description, setDescription] = useState('');
  const [usage, setUsage] = useState(0);
  const [updates, setUpdates] = useState(0);

  const [years, setYears] = useState('');
  const [niche, setNiche] = useState('');
  const [review, setReview] = useState('None');

  const [honorDate, setHonorDate] = useState('');
  const [honorLink, setHonorLink] = useState('');

  const [deleteDocument, setDeleteDocument] = useState(false);


  const isInvalid = honorName === '';

  const handlehonor = (event) => {
    event.preventDefault();

    db.collection('honor')
      .add({
        honorType: honorType,
        honorName: honorName,
        honorScope: honorScope,
        description: description,
        usage: usage,
        updates: updates,
        years: years,
        niche: niche,
        review: review,
        honorDate: honorDate,
        honorLink: honorLink,
        createdAt: new Date(),
        createdBy: user.uid,
        deleteDocument: deleteDocument,
        users: [
          {
            userId: user.uid,
          },
        ],
      })
      .then(() => {
        setLoader(false);
        alert('Your honor has been saved!');
      })
      .catch((error) => {
        alert(error.message);
        setLoader(false);
      });

    setHonorType('');
    setHonorName('');
    setHonorScope('None');
    setHonorDate('');
    setHonorLink('');

    setDescription('');
    setUsage(0);
    setUpdates(0);
    setYears('');
    setNiche('');
    setReview('None');
    setDeleteDocument(false);
  };

  return (
    <>
      <Form>
        <Form.Title>Add Honor</Form.Title>
        {error && <Form.Error>{error}</Form.Error>}

        <Form.Base onSubmit={handlehonor} method="POST">
          <Form.Input
            placeholder="Honor Type"
            data-lpignore="true"
            value={honorType}
            onChange={({ target }) => setHonorType(target.value)}
          />

          <Form.Input
            placeholder="Honor Name"
            data-lpignore="true"
            value={honorName}
            onChange={({ target }) => setHonorName(target.value)}
          />

          <label>
            <Form.Text>Choose Scope:</Form.Text>
            <select
              value={honorScope}
              onChange={({ target }) => setHonorScope(target.value)}
            >
              {' '}
              <option value="Regional">Regional</option>
              <option value="National">National</option>
              <option value="Global">Global</option>
              <option value="None">None</option>
            </select>
          </label>

          <Form.Input
            placeholder="Description"
            data-lpignore="true"
            value={description}
            onChange={({ target }) => setDescription(target.value)}
          />
          <Form.Input
            placeholder="Honor Date"
            type="date"
            value={honorDate}
            onChange={({ target }) => setHonorDate(target.value)}
          />

          <Form.Input
            placeholder="Years"
            data-lpignore="true"
            value={years}
            onChange={({ target }) => setYears(target.value)}
          />

          <Form.Input
            placeholder="Niche"
            data-lpignore="true"
            value={niche}
            onChange={({ target }) => setNiche(target.value)}
          />

          <label>
            <Form.Text>Choose Review:</Form.Text>
            <select
              value={review}
              onChange={({ target }) => setReview(target.value)}
            >
              {' '}
              <option value="Draft">Draft</option>
              <option value="Review">Review</option>
              <option value="Final">Final</option>
              <option value="None">None</option>
            </select>
          </label>

          <Form.Input
            placeholder="Honor Link"
            data-lpignore="true"
            value={honorLink}
            onChange={({ target }) => setHonorLink(target.value)}
          />
          <Form.Submit disabled={isInvalid} type="submit" data-testid="sign-up">
            Save Honor
          </Form.Submit>
        </Form.Base>

        <Form.Text>
          Want to see Honors? <Form.Link to="/honorview">View Honors</Form.Link>
        </Form.Text>
        <Form.Link to="/menu-stories-view">Return to Stories</Form.Link>
        <Form.Link to="/browse">Return to Browse</Form.Link>
        <Form.TextSmall>You are so awesome!</Form.TextSmall>
      </Form>
    </>
  );
}
