import React, { useState, useEffect, useContext } from 'react';
import Fuse from 'fuse.js';
import { Card, Header, Loading, Player } from '../components';
import * as ROUTES from '../constants/routes';

//SK Form link
import { Form } from '../components';

//import logo from '../logo.svg';
//import logo from '../logo-uni.png';
import logo from '../universily-header-logo.jpeg';

//SK
// import logoYoutube from '../social/youtube.png';
// import logoInstagram from '../social/instagram.png';
// import logoFacebook from '../social/facebook.png';
// import logoLinkedIn from '../social/linkedin.png';
// import logoTwitter from '../social/twitter.png';
// import logoUniEmail from '../social/universily-email-logo.jpeg';

//SK
import { Feature } from '../components';

import { FirebaseContext } from '../context/firebase';
import { FeatureLink } from '../components/card/styles/card';
//import { SelectProfileContainer } from './profiles';

export function BrowseContainer({ slides }) {
  const [category, setCategory] = useState('stories');
  const [profile] = useState({});
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [slideRows, setSlideRows] = useState([]);

  const { firebase } = useContext(FirebaseContext);
  const user = firebase.auth().currentUser || {};

  useEffect(() => {
    setTimeout(() => {
      setLoading(false);
    }, 3000);
  }, [profile.displayName]);

  useEffect(() => {
    setSlideRows(slides[category]);
  }, [slides, category]);

  useEffect(() => {
    const fuse = new Fuse(slideRows, {
      keys: ['data.description', 'data.title', 'data.genre'],
    });
    const results = fuse.search(searchTerm).map(({ item }) => item);

    if (slideRows.length > 0 && searchTerm.length > 3 && results.length > 0) {
      setSlideRows(results);
    } else {
      setSlideRows(slides[category]);
    }
  }, [searchTerm]);

  return (
    <>
      <Header dontShowOnSmallViewPort>
        <Header.Frame>
          <Header.Group>
            <Header.Logo to={ROUTES.HOME} src={logo} alt="Universily" />
            <Form.Link to="/menu-stories-view">Stories</Form.Link>
            <Form.Text>{String.fromCharCode(160)}</Form.Text>

            <Form.Link to="/menu-applications-view">Applications</Form.Link>
          </Header.Group>

          {/* <Header.Group>
            <Header.Logo to={ROUTES.SOCIAL} src={logoYoutube} alt="Youtube" />
            <Header.Logo to={ROUTES.SOCIAL} src={logoInstagram} alt="Insta" />
            <Header.Logo to={ROUTES.SOCIAL} src={logoFacebook} alt="Facebook" />
            <Header.Logo to={ROUTES.SOCIAL} src={logoLinkedIn} alt="LinkedIn" />
            <Header.Logo to={ROUTES.SOCIAL} src={logoTwitter} alt="Twitter" />
            <Header.Logo to={ROUTES.SOCIAL} src={logoUniEmail} alt="Logo" />
          </Header.Group> */}

          <Header.Group>
            <Header.Search
              searchTerm={searchTerm}
              setSearchTerm={setSearchTerm}
            />
            <Header.Profile>
              <Header.Picture src={user.photoURL} />
              <Header.Dropdown>
                <Header.Group>
                  <Header.Picture src={user.photoURL} />
                  <Header.TextLink>{user.displayName}</Header.TextLink>
                </Header.Group>
                <Header.Group>
                  <Header.TextLink onClick={() => firebase.auth().signOut()}>
                    Sign out
                  </Header.TextLink>
                </Header.Group>
              </Header.Dropdown>
            </Header.Profile>
          </Header.Group>
        </Header.Frame>
      </Header>
      <Feature>
        <Feature.SubTitle>
          Simplify the College Application Process
        </Feature.SubTitle>
        <Feature.Link
          active={category === 'stories' ? 'true' : 'false'}
          onClick={() => setCategory('stories')}
        >
          Story Guidance
        </Feature.Link>
        <Feature.Link
          active={category === 'applications' ? 'true' : 'false'}
          onClick={() => setCategory('applications')}
        >
          Application Guidance
        </Feature.Link>

        <Form.Text>{String.fromCharCode(160)}</Form.Text>
        {/* 
        <Feature.Link to="/menu-stories-view">Write your Stories!</Feature.Link>
        <Feature.Link to="/menu-applications-view">
          Create your Applications!
        </Feature.Link>

        <Form.Text>{String.fromCharCode(160)}</Form.Text>

        <Feature.Link to="/journey">Start your Journey!</Feature.Link>
    */}
      </Feature>

      <Card.Group>
        {slideRows.map((slideItem) => (
          <Card key={`${category}-${slideItem.title.toLowerCase()}`}>
            <Card.Title>{slideItem.title}</Card.Title>
            <Card.Entities>
              {slideItem.data.map((item) => (
                <Card.Item key={item.docId} item={item}>
                  <Card.Image
                    src={`/images/${category}/${item.genre}/${item.slug}/small.jpg`}
                  />
                  <Card.Meta>
                    <Card.SubTitle>{item.title}</Card.SubTitle>
                    <Card.Text>{item.description}</Card.Text>
                  </Card.Meta>
                </Card.Item>
              ))}
            </Card.Entities>
            <Card.Feature category={category}>
              <Player>
                <Player.Button />
                <Player.Video src="/videos/bunny.mp4" />
              </Player>
            </Card.Feature>
          </Card>
        ))}
      </Card.Group>
      {/* <FooterContainer /> */}
    </>
  );
}
