import React, { useState, useContext } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';
//SK
import { db } from '../context/firebase';

export default function Award() {
  const { firebase } = useContext(FirebaseContext);

  const [error] = useState('');
  //SK
  var user = firebase.auth().currentUser;
 // const [loader, setLoader] = useState(false);

  const [awardType, setAwardType] = useState('');
  const [awardName, setAwardName] = useState('');
  const [awardScope, setAwardScope] = useState('None');

  const [description, setDescription] = useState('');

  const [awardDate, setAwardDate] = useState('');
  const [awardLink, setAwardLink] = useState('');

  const [usage, setUsage] = useState(0);
  const [updates, setUpdates] = useState(0);

  const [years, setYears] = useState('');
  const [niche, setNiche] = useState('');
  const [review, setReview] = useState('None');

  const [deleteDocument, setDeleteDocument] = useState(false);

  const isInvalid = awardName === '';

  const handleAward = (event) => {
    event.preventDefault();

    //        createdAt: firebase.firestore.FieldValue.serverTimestamp(),

    db.collection('award')
      .add({
        awardType: awardType,
        awardName: awardName,
        awardScope: awardScope,
        description: description,
        awardDate: awardDate,
        awardLink: awardLink,
        createdAt: new Date(),
        createdBy: user.uid,
        updatedAt: new Date(),
        updatedBy: user.uid,
        usage: usage,
        updates: updates,
        years: years,
        niche: niche,
        review: review,
        deleteDocument: deleteDocument,
        users: [
          {
            userId: user.uid,
          },
        ],
      })
      .then(() => {
     //   setLoader(false);
        alert('Your award has been saved!');
      })
      .catch((error) => {
        alert(error.message);
      //  setLoader(false);
      });
    setAwardType('');
    setAwardName('');
    setAwardScope('');

    setDescription('');
    setAwardDate('');
    setAwardLink('');

    setUsage(0);
    setUpdates(0);
    setYears('');
    setNiche('');
    setReview('None');
    setDeleteDocument(false);
  };

  return (
    <>
      <Form>
        <Form.Title>Add Award</Form.Title>
        {error && <Form.Error>{error}</Form.Error>}

        <Form.Base onSubmit={handleAward} method="POST">
          <Form.Input
            placeholder="Award Type"
            value={awardType}
            data-lpignore="true"
            onChange={({ target }) => setAwardType(target.value)}
          />

          <Form.Input
            placeholder="Award Name"
            value={awardName}
            data-lpignore="true"
            onChange={({ target }) => setAwardName(target.value)}
          />

          <Form.Input
            placeholder="Description"
            value={description}
            data-lpignore="true"
            onChange={({ target }) => setDescription(target.value)}
          />
          <Form.Input
            placeholder="Award Date"
            type="date"
            value={awardDate}
            data-lpignore="true"
            onChange={({ target }) => setAwardDate(target.value)}
          />
          <Form.Input
            placeholder="Award Link"
            value={awardLink}
            data-lpignore="true"
            onChange={({ target }) => setAwardLink(target.value)}
          />

          <Form.Submit disabled={isInvalid} type="submit" data-testid="sign-up">
            Save Award
          </Form.Submit>
        </Form.Base>

        <Form.Text>
          Want to see Awards? <Form.Link to="/awardview">View Awards</Form.Link>
        </Form.Text>
        <Form.Link to="/browse">Return to Browse</Form.Link>
        <Form.TextSmall>You are so awesome!</Form.TextSmall>
      </Form>
    </>
  );
}
