import React, { useState, useContext } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//SK
import { db } from '../context/firebase';

import Grid from '../components/grid';
import { SidebarStories } from '../components/layout/sidebar-stories';
import { UniHeader } from '../containers/universilyheader';
import { FooterContainer } from '../containers/footer';

export default function Testing() {
  return (
    <Grid>
      <Grid.Header>
        <UniHeader />
      </Grid.Header>
      <Grid.Main>
        <TestingPage />
      </Grid.Main>
      <Grid.Aside>
        <SidebarStories />
      </Grid.Aside>
      <Grid.Footer>
        <FooterContainer />
      </Grid.Footer>
    </Grid>
  );
}

export function TestingPage() {
  const { firebase } = useContext(FirebaseContext);

  const [error] = useState('');
  //SK
  var user = firebase.auth().currentUser;
  const [loader, setLoader] = useState(false);

  const [testType, setTestType] = useState('');
  const [testName, setTestName] = useState('');
  const [description, setDescription] = useState('');
  const [usage, setUsage] = useState(0);
  const [updates, setUpdates] = useState(0);

  const [testDate, setTestDate] = useState('');
  const [status, setStatus] = useState('None');

  const [testLink, setTestLink] = useState('');

  const isInvalid = testName === '';

  const handleTesting = (event) => {
    event.preventDefault();

    db.collection('testing')
      .add({
        testType: testType,
        testName: testName,
        description: description,
        usage: usage,
        updates: updates,
        testDate: testDate,
        testLink: testLink,
        createdAt: new Date(),
        createdBy: user.uid,
        users: [
          {
            userId: user.uid,
          },
        ],
      })
      .then(() => {
        setLoader(false);
        alert('Your testing has been saved!');
      })
      .catch((error) => {
        alert(error.message);
        setLoader(false);
      });
    setTestType('');
    setTestName('');

    setDescription('');
    setUsage(0);
    setUpdates(0);

    setTestDate('');
    setStatus('None');

    setTestLink('');
  };

  return (
    <div className="testing">
      <>
        <Form>
          <Form.Title>Add Test</Form.Title>
          {error && <Form.Error>{error}</Form.Error>}

          <Form.Base onSubmit={handleTesting} method="POST">
            <Form.Input
              placeholder="Test Type"
              data-lpignore="true"
              value={testType}
              onChange={({ target }) => setTestType(target.value)}
            />

            <Form.Input
              placeholder="Test Name"
              data-lpignore="true"
              value={testName}
              onChange={({ target }) => setTestName(target.value)}
            />

            <Form.Input
              placeholder="Description"
              data-lpignore="true"
              value={description}
              onChange={({ target }) => setDescription(target.value)}
            />
            <Form.Input
              placeholder="Test Date"
              type="date"
              value={testDate}
              onChange={({ target }) => setTestDate(target.value)}
            />

            <label>
              <Form.Text>Choose Status:</Form.Text>
              <select
                value={status}
                onChange={({ target }) => setStatus(target.value)}
              >
                {' '}
                <option value="Current">Current</option>
                <option value="Planned">Planned</option>
                <option value="Completed">Completed</option>
                <option value="None">None</option>
              </select>
            </label>

            <Form.Input
              placeholder="Test Link"
              data-lpignore="true"
              value={testLink}
              onChange={({ target }) => setTestLink(target.value)}
            />
            <Form.Submit
              disabled={isInvalid}
              type="submit"
              data-testid="testing"
            >
              Save Test
            </Form.Submit>
          </Form.Base>

          <Form.Text>
            Want to see Tests?{' '}
            <Form.Link to="/testingview">View Tests</Form.Link>
          </Form.Text>
          <Form.Link to="/browse">Return to Browse</Form.Link>
          <Form.TextSmall>You are so awesome!</Form.TextSmall>
        </Form>
      </>
    </div>
  );
}
