export default function selectionFilter({ stories, applications } = []) {
  return {
    stories: [
      {
        title: 'Activities',
        data: stories?.filter((item) => item.genre === 'activities'),
      },
      {
        title: 'Awards',
        data: stories?.filter((item) => item.genre === 'awards'),
      },
      {
        title: 'Demographics',
        data: stories?.filter((item) => item.genre === 'demographics'),
      },
      {
        title: 'Essays',
        data: stories?.filter((item) => item.genre === 'essays'),
      },
      {
        title: 'Niche',
        data: stories?.filter((item) => item.genre === 'niche'),
      },
    ],
    applications: [
      {
        title: 'Colleges',
        data: applications?.filter((item) => item.genre === 'colleges'),
      },
      {
        title: 'Rounds',
        data: applications?.filter((item) => item.genre === 'rounds'),
      },
      {
        title: 'Types',
        data: applications?.filter((item) => item.genre === 'types'),
      },
      {
        title: 'Proflle',
        data: applications?.filter((item) => item.genre === 'profile'),
      },
      {
        title: 'Recommendations',
        data: applications?.filter((item) => item.genre === 'recommendations'),
      },
    ],
  };
}


