import React, { useState, useEffect } from 'react';
import { Form } from '../components';

//SK
import { GuidanceContainer } from '../containers/guidance';
import { useContent } from '../hooks';
import { selectionFilter } from '../utils';

//SK
import { db } from '../context/firebase';



 function selectionFilterActivities({ stories, applications } = []) {
    return {
      stories: [
        {
          title: 'Activities',
          data: stories?.filter((item) => item.genre === 'activities'),
        },
        
      ],
    };
  };


//SK Must pass in props to get docID and DocType.
export default function GuidanceView(props) {
  const [error, setError] = useState('');
  //SK
  const [showText, setShowText] = useState (false);

  const [guidances, setGuidances] = useState([]);
  const [countGuidances, setCountGuidances] = useState(0);

  const { stories } = useContent('stories');
  const { applications } = useContent('applications');
  //const slides = selectionFilter({ stories, applications });
  //const [slides, setSlides] = useState([]);

    const slides = selectionFilterActivities({ stories, applications });


  //console.log('slides:' + JSON.stringify({slides}));


  //SK Props
  console.log('guidanceview.js: props: guidanceGenre:' + props.guidanceGenre);

  // SK
  const [userLocal] = useState(JSON.parse(localStorage.getItem('authUser')));


  useEffect(() => {
    db.collection('stories')
      .where('genre', '==', props.guidanceGenre)
      .get()
      .then((response) => {
        const fetchedGuidances = [];
        response.docs.forEach((document) => {
          const fetchedGuidance = {
            id: document.id,
            ...document.data(),
          };
          fetchedGuidances.push(fetchedGuidance);
        });
        setGuidances(fetchedGuidances);
        setCountGuidances(fetchedGuidances.length);

      //setSlides(fetchedGuidances);
      //console.log('slides:' + JSON.stringify({slides}));

        console.log('guidanceview.js: setCountGuidances:' + countGuidances);

      })
      .catch((error) => {
        setError(error);
      });
  }, []);

  return countGuidances > -10 ? (
    <>
    <div>
    <button 
    onClick= {()=> {
      setShowText(!showText);
      console.log ("showText: " + showText);
    }

    }>View Guidance</button>


{showText && 
    <div>
    
          <GuidanceContainer slides={slides}/>

      </div>
    }
 
      </div>
    </>
  ) : null;
};
