import React, { useState, useContext } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//SK
import { db } from '../context/firebase';

export default function TestingUpdate(props) {
  const { firebase } = useContext(FirebaseContext);

  const [error] = useState('');
  //SK
  var user = firebase.auth().currentUser;
  const [setLoader] = useState(false);

  //SK Set the docID passed from the link.
  //setDocumentID({ props.docID });
  //console.log('props:' + JSON.stringify(props));
  //console.log('props:props.docObject:  ' + JSON.stringify(props.docObject));
  //console.log('props: Update Niche: documentID:' + props.docID);

  // const [documentID, setDocumentID] = useState(props.docObject.ID);

  const [testType, setTestType] = useState(props.docObject.testType);
  const [testName, setTestName] = useState(props.docObject.testName);
  const [description, setDescription] = useState(props.docObject.description);

  const [usage] = useState(props.docObject.usage);
  const [updates] = useState(props.docObject.updates + 1);

  const [testDate, setTestDate] = useState(props.docObject.testDate);
  const [status, setStatus] = useState(props.docObject.status);

  const [testLink, setTestLink] = useState(props.docObject.testLink);

  const isInvalid = testName === '';

  const handleTestingUpdate = (event) => {
    event.preventDefault();

    db.collection('testing')
      .doc(props.docID)
      .set(
        {
          testType: testType,
          testName: testName,
          description: description,
          usage: usage,
          updates: updates,
          testDate: testDate,
          testLink: testLink,
          status: status,
          updatedAt: new Date(),
          updatedBy: user.uid,
          users: [
            {
              userId: user.uid,
            },
          ],
        },
        { merge: true }
      )
      .then(() => {
        setLoader(false);
        alert('Your testing has been updated!');
      })
      .catch((error) => {
        alert(error.message);
        setLoader(false);
      });
  };

  return (
    <>
      <Form>
        <Form.Title>Update Testing</Form.Title>
        {error && <Form.Error>{error}</Form.Error>}

        <Form.Base onSubmit={handleTestingUpdate} method="POST">
          <Form.Input
            placeholder="Test Type"
            data-lpignore="true"
            value={testType}
            onChange={({ target }) => setTestType(target.value)}
          />

          <Form.Input
            placeholder="Test Name"
            data-lpignore="true"
            value={testName}
            onChange={({ target }) => setTestName(target.value)}
          />

          <Form.Input
            placeholder="Description"
            data-lpignore="true"
            value={description}
            onChange={({ target }) => setDescription(target.value)}
          />

          <Form.Input
            placeholder="Test Date"
            type="date"
            value={testDate}
            onChange={({ target }) => setTestDate(target.value)}
          />

          <label>
            <Form.Text>Choose Status:</Form.Text>
            <select
              value={status}
              onChange={({ target }) => setStatus(target.value)}
            >
              {' '}
              <option value="Current">Current</option>
              <option value="Planned">Planned</option>
              <option value="Completed">Completed</option>
              <option value="None">None</option>
            </select>
          </label>

          <Form.Input
            placeholder="Test Link"
            data-lpignore="true"
            value={testLink}
            onChange={({ target }) => setTestLink(target.value)}
          />
          <Form.Submit disabled={isInvalid} type="submit" data-testid="testing">
            Save Testing
          </Form.Submit>
        </Form.Base>

        <Form.Text>
          Want to see Testings?{' '}
          <Form.Link to="/testingview">View Testings</Form.Link>
        </Form.Text>

        <Form.Link to="/browse">Return to Browse</Form.Link>
        <Form.TextSmall>You are so awesome!</Form.TextSmall>
      </Form>
    </>
  );
}
