import React, { useState, useContext, useEffect } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';
import email from '../social/mail.jpeg';

//import * as ROUTES from '../constants/routes';

//SK
import { db } from '../context/firebase';

//SK Must pass in props to get docID and DocType.
export default function ReviewersView(props) {
  const { firebase } = useContext(FirebaseContext);

  const [error, setError] = useState('');
  //SK
  //var user = firebase.auth().currentUser;
  const [reviewers, setReviewers] = useState([]);
  const [countReviewers, setCountReviewers] = useState(0);

  // SK
  const [userLocal] = useState(JSON.parse(localStorage.getItem('authUser')));

  firebase.auth().onAuthStateChanged(function (user) {
    if (user) {
      // User is signed in.
      //uid = user.uid;
      console.log(
        'user is signed in for onAuthStateChanged: reviewersview: uid:' +
          userLocal.uid
      );
    } else {
      // No user is signed in.
      console.log(
        'user is NOT signed in for onAuthStateChanged: reviewersview.'
      );
      //uid = '';
    }
  });

  // SK mail to:                   href="mailto:info@universily.com"
  //                   href="mailto:info@universily.com?subject=Universily Invite&body=TEST"

  //.where('reviewee', '==', userLocal.uid)
  //.where('docID', '==', props.docID)
  //.where('commentType', '==', props.docType)

  // SK get the length from fetched Comments.
  //setCountComments(fetchedComments.length);

  useEffect(() => {
    db.collection('reviewer')
      .where('reviewee', '==', userLocal.uid)
      .get()
      .then((response) => {
        const fetchedReviewers = [];
        response.docs.forEach((document) => {
          const fetchedReviewer = {
            id: document.id,
            ...document.data(),
          };
          fetchedReviewers.push(fetchedReviewer);
        });
        setReviewers(fetchedReviewers);
        setCountReviewers(fetchedReviewers.length);
      })
      .catch((error) => {
        setError(error);
      });
  }, []);

  return countReviewers > 0 ? (
    <>
      <Form.SubTitle>View Reviewers</Form.SubTitle>
      {error && <Form.Error>{error}</Form.Error>}
      <Form.Table>
        <Form.Thead>
          <Form.Tr>
            <Form.Th>Name</Form.Th>
            <Form.Th>Email</Form.Th>
            <Form.Th>Description</Form.Th>
            <Form.Th>Links</Form.Th>
            <Form.Th>Invite</Form.Th>
          </Form.Tr>
        </Form.Thead>
        <Form.Tbody>
          {error ? <p>Ops, there is an error :(</p> : null}
          {reviewers.map((reviewer) => (
            <Form.Tr key={reviewer.id}>
              <Form.Td> {reviewer.reviewerName}</Form.Td>
              <Form.Td> {reviewer.reviewerEmail}</Form.Td>
              <Form.Td> {reviewer.description}</Form.Td>
              <Form.Td> {reviewer.reviewerLink}</Form.Td>
              <Form.Td>
                <Form.EmailLink
                  emailLink={reviewer.reviewerEmail}
                  email={email}
                  subject="Universily Invite"
                  body="You are invited to join Universily to review my application."
                  universilyUrl="https://universily.net"
                />
              </Form.Td>
            </Form.Tr>
          ))}
        </Form.Tbody>
      </Form.Table>
    </>
  ) : null;
}
