import React, { useState, useContext } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//SK
import { db } from '../context/firebase';

export default function Admin() {
  const { firebase } = useContext(FirebaseContext);

  const [error] = useState('');
  //SK
  var user = firebase.auth().currentUser;
  const [setLoader] = useState(false);

  const [genre, setGenre] = useState('none');
  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');
  const [menu, setMenu] = useState('');
  const [menuname, setMenuname] = useState('');

  const [slug, setSlug] = useState('');
  const [link, setLink] = useState('');


  const [usage, setUsage] = useState(0);
  const [updates, setUpdates] = useState(0);

  const [deleteDocument, setDeleteDocument] = useState(false);


  const isInvalid = title === '';

  const handleAdminStoryGuidance = (event) => {
    event.preventDefault();


    db.collection('stories')
      .add({
        genre: genre,
        title: title,
        description: description,
        menu: menu,
        menuname: menuname,
        maturity: '16',
        rating: 5,
        slug: slug,
        link: link,
        createdAt: new Date(),
        createdBy: user.uid,
        usage: usage,
        updates: updates,
        slug: slug,
        link: link,
        updatedAt: new Date(),
        updatedBy: user.uid,
        deleteDocument: deleteDocument,
      })
      .then(() => {
        setLoader(false);
        alert('Your Admin Story Guidance has been saved!');
      })
      .catch((error) => {
        alert(error.message);
        setLoader(false);
      });
    setGenre('none');
    setTitle('');

    setDescription('');
    setMenu('');
    setMenuname('');

    setUsage(0);
    setUpdates(0);

    setSlug('');
    setLink('');
  };

  return (
    <>
      <Form>
        <Form.Title>Add Admin Story Guidance</Form.Title>
        {error && <Form.Error>{error}</Form.Error>}

        <Form.Base onSubmit={handleAdminStoryGuidance} method="POST">
          <label>
            <Form.Text>Choose Genre:</Form.Text>
            <select
              value={genre}
              onChange={({ target }) => setGenre(target.value)}
            >
              {' '}
              <option value="activities">Activities</option>
              <option value="awards">Awards</option>
              <option value="academics">Academics</option>
              <option value="demographics">Demographics</option>
              <option value="essays">Essays </option>
              <option value="other">Other</option>
              <option value="none">None</option>
            </select>
          </label>

          <Form.Input
            placeholder="Title *Required*"
            data-lpignore="true"
            value={title}
            onChange={({ target }) => setTitle(target.value)}
          />

          <Form.Input
            placeholder="Description"
            data-lpignore="true"
            type="textarea"
            value={description}
            onChange={({ target }) => setDescription(target.value)}
          />

          <Form.Input
            placeholder="Menu (example: Add Activities)"
            data-lpignore="true"
            type={Text}
            value={menu}
            onChange={({ target }) => setMenu(target.value)}
          />

          <Form.Input
            placeholder="Menu Name (example: /activity)"
            data-lpignore="true"
            type={Text}
            value={menuname}
            onChange={({ target }) => setMenuname(target.value)}
          />

          <Form.Input
            placeholder="Slug"
            type={Text}
            value={slug}
            onChange={({ target }) => setSlug(target.value)}
          />
          <Form.Input
            placeholder="Link"
            data-lpignore="true"
            value={link}
            onChange={({ target }) => setLink(target.value)}
          />
          <Form.Submit disabled={isInvalid} type="submit" data-testid="admin-story">
            Save Guidance
          </Form.Submit>
        </Form.Base>

        <Form.Text>
          Want to see your Guidance?{' '}
          <Form.Link to="/adminview">View Admin Story Guidance</Form.Link>
        </Form.Text>
        <Form.Link to="/admin">Add Admin Story Guidance</Form.Link>
        <Form.Link to="/browse">Return to Browse</Form.Link>
        <Form.TextSmall>You give some Awesome Guidance!</Form.TextSmall>
      </Form>
    </>
  );
}
