import React, { useState, useContext, useEffect } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//SK
import { db } from '../context/firebase';

//SK Google Drive
import drive from '../social/drive.png';

//SK Update
import AdminStoryGuidanceUpdate from './adminupdate';

import Grid from '../components/grid';
import { SidebarAdmin } from '../components/layout/sidebar-admin';
import { UniHeader } from '../containers/universilyheader';
import { FooterContainer } from '../containers/footer';

export default function AdminView() {
  return (
    <Grid>
      <Grid.Header>
        <UniHeader />
      </Grid.Header>
      <Grid.Main>
        <AdminViewPage />
      </Grid.Main>
      <Grid.Aside>
        <SidebarAdmin />
      </Grid.Aside>
      <Grid.Footer>
        <FooterContainer />
      </Grid.Footer>
    </Grid>
  );
}

export function AdminViewPage() {
  const [error, setError] = useState('');

  //SK
  const [stories, setStories] = useState([]);

  // SK
  const [userLocal, setUserLocal] = useState(
    JSON.parse(localStorage.getItem('authUser'))
  );

  // SK View Update Details
  const [showText, setShowText] = useState(false);
  const [updateDocument, setUpdateDocument] = useState(false);

  useEffect(() => {
    db.collection('stories')
      .get()
      .then((response) => {
        const fetchedStories = [];
        response.docs.forEach((document) => {
          const fetchedStory = {
            id: document.id,
            ...document.data(),
          };
          fetchedStories.push(fetchedStory);
        });
        setStories(fetchedStories);
      })
      .catch((error) => {
        setError(error);
      });
  }, []);

  return (
    <>
      <Form>
        <Form.SubTitle>View Stories Guidance</Form.SubTitle>
        {error && <Form.Error>{error}</Form.Error>}
        <Form.Table>
          <Form.Thead>
            <Form.Tr>
              <Form.Th>Genre</Form.Th>
              <Form.Th>Title</Form.Th>

              <Form.Th>Description</Form.Th>

              <Form.Th>Menu</Form.Th>
              <Form.Th>Menu Name</Form.Th>

              <Form.Th>Rating</Form.Th>

              <Form.Th>Slug</Form.Th>
              <Form.Th>ID</Form.Th>
              <Form.Th>Update</Form.Th>
            </Form.Tr>
          </Form.Thead>
          <Form.Tbody>
            {error ? <p>Ops, there is an error :(</p> : null}
            {stories.map((story) => (
              <Form.Tr key={story.id}>
                <Form.Td> {story.genre}</Form.Td>
                <Form.Td> {story.title}</Form.Td>

                <Form.Td> {story.description}</Form.Td>
                <Form.Td> {story.menu}</Form.Td>
                <Form.Td> {story.menuname}</Form.Td>

                <Form.Td> {story.rating}</Form.Td>

                <Form.Td> {story.slug}</Form.Td>
                <Form.Td> {story.id}</Form.Td>

                <Form.Td>
                  <AdminStoryGuidanceUpdate
                    createdBy={userLocal.uid}
                    docID={story.id}
                    docObject={story}
                  />
                </Form.Td>
              </Form.Tr>
            ))}
          </Form.Tbody>
        </Form.Table>
        <Form.Link to="/adminview">View Admin Story Guidance</Form.Link>
        <Form.Link to="/admin">Add Admin Story Guidance</Form.Link>

        <Form.Link to="/adminview">View Admin Story Guidance</Form.Link>
        <Form.Link to="/admin">Add Admin Story Guidance</Form.Link>
        <Form.Link to="/browse">Return to Browse</Form.Link>
        <Form.TextSmall>You give some Awesome Guidance!</Form.TextSmall>
      </Form>
    </>
  );
}
