import React, { useState, useContext } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//SK
import { db } from '../context/firebase';

//SK accepts props.
export default function Reviewer(props) {
  const { firebase } = useContext(FirebaseContext);

  const [error] = useState('');
  const [refresh, setRefresh] = useState('');

  //SK
  var user = firebase.auth().currentUser;
  const [setLoader] = useState(false);

  //SK Set the docID passed from the link.
  //setDocumentID({ props.docID });
  console.log('reviewer:props:' + JSON.stringify(props));
  console.log('reviewer:props: docID:' + props.docID);

  const [docID, setDocID] = useState(props.docID);
  const [reviewerType, setReviewerType] = useState(props.docType);
  const [reviewerName, setReviewerName] = useState('');
  const [reviewerEmail, setReviewerEmail] = useState('');

  const [description, setDescription] = useState('');
  const [status, setStatus] = useState('New');

  const [reviewerDate, setReviewerDate] = useState('');
  const [reviewerLink, setReviewerLink] = useState('');
  const [reviewee, setReviewee] = useState(props.reviewee);

  // SK
  //   {options.map((option) => (<option value={option.value}>{option.label}</option>))}

  //const isInvalid = Name === '' || password === '' || emailAddress === '';
  const isInvalid = reviewerName === '' || reviewerEmail === '';

  const handleReviewer = (event) => {
    event.preventDefault();

    //        createdAt: firebase.firestore.FieldValue.serverTimestamp(),

    db.collection('reviewer')
      .add({
        docID: docID,
        reviewerType: reviewerType,
        reviewerName: reviewerName,
        reviewerEmail: reviewerEmail,
        description: description,
        status: status,
        reviewerDate: new Date(),
        reviewerLink: reviewerLink,
        createdAt: new Date(),
        createdBy: user.uid,
        reviewee: user.uid,
        users: [
          {
            userId: user.uid,
          },
        ],
      })
      .then(() => {
        setLoader(false);
        alert('Your reviewer has been saved!');
      })
      .catch((error) => {
        alert(error.message);
        setLoader(false);
      });

    setDocID('');

    setReviewerType('');
    setReviewerName('');
    setReviewerEmail('');

    setDescription('');
    setStatus('');

    setReviewee('');

    setReviewerDate('');
    setReviewerLink('');

    setRefresh(1);
    console.log('refresh::' + refresh);
  };

  return (
    <>
      <Form>
        <Form.Text>Add Reviewer</Form.Text>
        {error && <Form.Error>{error}</Form.Error>}

        <Form.Base onSubmit={handleReviewer} method="POST">
          <Form.Input
            placeholder="Reviewer Name"
            type={Text}
            data-lpignore="true"
            value={reviewerName}
            onChange={({ target }) => setReviewerName(target.value)}
          />

          <Form.Input
            placeholder="Reviewer Email"
            type={Text}
            data-lpignore="true"
            value={reviewerEmail}
            onChange={({ target }) => setReviewerEmail(target.value)}
          />

          <Form.InputLongMedium
            placeholder="Description"
            type="textarea"
            data-lpignore="true"
            value={description}
            onChange={({ target }) => setDescription(target.value)}
          />

          <Form.Input
            placeholder="Reviewer Link"
            type={URL}
            data-lpignore="true"
            value={reviewerLink}
            onChange={({ target }) => setReviewerLink(target.value)}
          />

          <Form.Submit disabled={isInvalid} type="submit" data-testid="sign-up">
            Save Reviewer
          </Form.Submit>
        </Form.Base>

        <Form.TextSmall>Your reviewer is so awesome!</Form.TextSmall>
      </Form>
    </>
  );
}
