import React, { useState, useContext, useEffect } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';
import Comment from './comment';

//SK
import { db } from '../context/firebase';

//SK Google Drive
import drive from '../social/drive.png';

//SK input props.
export default function ActivityReview(props) {

  const [error, setError] = useState('');
  
  const [activities, setActivities] = useState([]);
  // SK
  const [userLocal, setUserLocal] = useState(
    JSON.parse(localStorage.getItem('authUser'))
  );

  useEffect(() => {
    db.collection('activity')
      .where('reviewers', 'array-contains', userLocal.uid)
      .get()
      .then((response) => {
        const fetchedActivities = [];
        response.docs.forEach((document) => {
          const fetchedActivity = {
            id: document.id,
            ...document.data(),
          };
          fetchedActivities.push(fetchedActivity);
        });
        setActivities(fetchedActivities);
        console.log('activities:' + activities.count);
      })
      .catch((error) => {
        setError(error);
      });
  }, []);

  return (
    <>
      <Form>
        <Form.Title>Review Activities</Form.Title>
        {error && <Form.Error>{error}</Form.Error>}

        <Form.Text>
          {error ? <p>Ops, there is an error :(</p> : null}
          {activities.map((activity) => (
            <li key={activity.id}>{activity.activityName}</li>
          ))}
          Want to add more Activites?
          <Form.Link to="/activity">Add Activities</Form.Link>
        </Form.Text>

        <Form.Title>Shared Details</Form.Title>

        <Form.Table>
          {activities.map((activity) => (
            <Form.Tr key={activity.id}>
              <Form.Tr>
                <Form.Th>Field</Form.Th>
                <Form.Th>Value</Form.Th>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Activity Type</Form.Td>
                <Form.Td>{activity.activityType}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Activity Name</Form.Td>
                <Form.Td>{activity.activityName}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Description</Form.Td>
                <Form.Td>{activity.description}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Activity Date</Form.Td>
                <Form.Td>{activity.activityDate}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Scope</Form.Td>
                <Form.Td>{activity.activityScope}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Status</Form.Td>
                <Form.Td> {activity.status}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Review</Form.Td>
                <Form.Td> {activity.review}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Reviewers</Form.Td>
                <Form.Td> {activity.reviewers}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Drive Link</Form.Td>
                <Form.DriveLink
                  driveLink={activity.activityLink}
                  drive={drive}
                />
              </Form.Tr>

              <Form.Table>
                <Form.Tbody>
                  <Comment
                    docID={activity.id}
                    docType="activity"
                    reviewee={activity.createdBy}
                  />
                </Form.Tbody>
              </Form.Table>
            </Form.Tr>
          ))}
        </Form.Table>
        <Form.Link to="/activityview">Return to your Activity View</Form.Link>
        <Form.Link to="/menu-stories-view">Return to your Stories</Form.Link>

        <Form.Link to="/browse">Return to Browse</Form.Link>

        <Form.TextSmall>You are so awesome!</Form.TextSmall>
      </Form>
    </>
  );
}
