import React, { useState, useContext } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//SK
import { db } from '../context/firebase';

export default function Parent() {
  const { firebase } = useContext(FirebaseContext);

  const [error] = useState('');
  //SK
  var user = firebase.auth().currentUser;
  const [setLoader] = useState(false);

  const [parentType, setParentType] = useState('None');
  const [parentName, setParentName] = useState('');
  const [parentBirthplace, setParentBirthplace] = useState('');

  const [description, setDescription] = useState('');
  const [address, setAddress] = useState('');
  const [phone, setPhone] = useState('');

  const [occupation, setOccupation] = useState('');
  const [education, setEducation] = useState('');

  const [parentDate, setParentDate] = useState('');
  const [parentLink, setParentLink] = useState('');

  const isInvalid = parentName === '';

  const handleParent = (event) => {
    event.preventDefault();

    //        createdAt: firebase.firestore.FieldValue.serverTimestamp(),

    db.collection('parent')
      .add({
        uid: user.uid,
        parentType: parentType,
        parentName: parentName,
        parentBirthplace: parentBirthplace,
        description: description,

        address: address,
        phone: phone,

        occupation: occupation,
        education: education,

        parentDate: parentDate,
        parentLink: parentLink,

        createdAt: new Date(),
        createdBy: user.uid,
        users: [
          {
            userId: user.uid,
          },
        ],
      })
      .then(() => {
        setLoader(false);
        alert('Your parent has been saved!');
      })
      .catch((error) => {
        alert(error.message);
        setLoader(false);
      });
    setParentType('None');
    setParentName('');
    setParentBirthplace('');

    setDescription('');
    setAddress('');
    setPhone('');

    setOccupation('');
    setEducation('');

    setParentDate('');
    setParentLink('');
  };

  return (
    <>
      <Form>
        <Form.Title>Update Parent</Form.Title>
        {error && <Form.Error>{error}</Form.Error>}

        <Form.Base onSubmit={handleParent} method="POST">
          <label>
            <Form.Text>Choose Parent Type:</Form.Text>
            <select
              value={parentType}
              onChange={({ target }) => setParentType(target.value)}
            >
              {' '}
              <option value="Father">Father</option>
              <option value="Mother">Mother</option>
              <option value="One">Parent 1</option>
              <option value="Two">Parent 2</option>
              <option value="Other">Other</option>
              <option value="None">None</option>
            </select>
          </label>

          <Form.Input
            placeholder="Parent Name (Their Full Name) *Required*"
            data-lpignore="true"
            value={parentName}
            onChange={({ target }) => setParentName(target.value)}
          />

          <Form.Input
            placeholder="Parent Birthplace"
            data-lpignore="true"
            value={parentBirthplace}
            onChange={({ target }) => setParentBirthplace(target.value)}
          />

          <Form.Input
            placeholder="Description"
            data-lpignore="true"
            type="textarea"
            value={description}
            onChange={({ target }) => setDescription(target.value)}
          />

          <Form.Input
            placeholder="Full Address (Street, City, State, Zip)"
            data-lpignore="true"
            type={Text}
            value={address}
            onChange={({ target }) => setAddress(target.value)}
          />

          <Form.Input
            placeholder="Full Phone (+1-888-555-1212, Country, Area, Phone)"
            data-lpignore="true"
            type={Text}
            value={phone}
            onChange={({ target }) => setPhone(target.value)}
          />

          <Form.Input
            placeholder="Occupation"
            data-lpignore="true"
            type={Text}
            value={occupation}
            onChange={({ target }) => setOccupation(target.value)}
          />

          <Form.Input
            placeholder="Education"
            data-lpignore="true"
            type={Text}
            value={education}
            onChange={({ target }) => setEducation(target.value)}
          />

          <Form.Input
            placeholder="Parent Date"
            type="date"
            value={parentDate}
            onChange={({ target }) => setParentDate(target.value)}
          />
          <Form.Input
            placeholder="Parent Link"
            data-lpignore="true"
            value={parentLink}
            onChange={({ target }) => setParentLink(target.value)}
          />
          <Form.Submit disabled={isInvalid} type="submit" data-testid="sign-up">
            Save Parent
          </Form.Submit>
        </Form.Base>

        <Form.Text>
          Want to see your Parent?{' '}
          <Form.Link to="/parentview">View Parent</Form.Link>
        </Form.Text>
        <Form.Link to="/browse">Return to Browse</Form.Link>
        <Form.TextSmall>You are so awesome!</Form.TextSmall>
      </Form>
    </>
  );
}
