import React from 'react';
import {
  Container,
  Error,
  Base,
  Title,
  SubTitle,
  Text,
  TextSmall,
  Link,
  Input,
  InputLong,
  InputLongMedium,
  Submit,
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
} from './styles/form';

export default function Form({ children, ...restProps }) {
  return <Container {...restProps}>{children}</Container>;
}

Form.Error = function FormError({ children, ...restProps }) {
  return <Error {...restProps}>{children}</Error>;
};

Form.Base = function FormBase({ children, ...restProps }) {
  return <Base {...restProps}>{children}</Base>;
};

Form.Title = function FormTitle({ children, ...restProps }) {
  return <Title {...restProps}>{children}</Title>;
};

Form.SubTitle = function FormSubTitle({ children, ...restProps }) {
  return <SubTitle {...restProps}>{children}</SubTitle>;
};

Form.Text = function FormText({ children, ...restProps }) {
  return <Text {...restProps}>{children}</Text>;
};

Form.TextSmall = function FormTextSmall({ children, ...restProps }) {
  return <TextSmall {...restProps}>{children}</TextSmall>;
};

Form.Link = function FormLink({ children, ...restProps }) {
  return <Link {...restProps}>{children}</Link>;
};

Form.DriveLink = function FormDriveLink({ driveLink, drive }) {
  return driveLink ? (
    <a href={driveLink} target="_blank" rel="noopener noreferrer">
      <img
        src={drive}
        alt=""
        moz-do-not-send="true"
        border="0"
        height="20"
        width="20"
      />
    </a>
  ) : null;
};

Form.EmailLink = function FormEmailLink({
  emailLink,
  email,
  subject,
  body,
  universilyUrl,
}) {
  var emailtoBody = '&body=' + body + '   ' + universilyUrl;
  var emailtoLink = 'mailto:' + emailLink + '?subject=' + subject + emailtoBody;

  return emailLink ? (
    <a href={emailtoLink} target="_blank" rel="noopener noreferrer">
      <img
        src={email}
        alt=""
        moz-do-not-send="true"
        border="0"
        height="50"
        width="50"
      />
    </a>
  ) : null;
};

Form.Input = function FormInput({ children, ...restProps }) {
  return <Input {...restProps}>{children}</Input>;
};

Form.InputLong = function FormInputLong({ children, ...restProps }) {
  return <InputLong {...restProps}>{children}</InputLong>;
};

Form.InputLongMedium = function FormInputLongMedium({
  children,
  ...restProps
}) {
  return <InputLongMedium {...restProps}>{children}</InputLongMedium>;
};

Form.Submit = function FormSubmit({ children, ...restProps }) {
  return <Submit {...restProps}>{children}</Submit>;
};

Form.Table = function FormTable({ children, ...restProps }) {
  return <Table {...restProps}>{children}</Table>;
};

Form.Thead = function FormThead({ children, ...restProps }) {
  return <Thead {...restProps}>{children}</Thead>;
};

Form.Tbody = function FormTbody({ children, ...restProps }) {
  return <Tbody {...restProps}>{children}</Tbody>;
};

Form.Tr = function FormTr({ children, ...restProps }) {
  return <Tr {...restProps}>{children}</Tr>;
};

Form.Th = function FormTh({ children, ...restProps }) {
  return <Th {...restProps}>{children}</Th>;
};
Form.Td = function FormTd({ children, ...restProps }) {
  return <Td {...restProps}>{children}</Td>;
};
