import React from 'react';
import { Wrapper, Header, Main, Aside, Footer } from './styles/grid';

export default function Grid({ children, ...restProps }) {
  return <Wrapper {...restProps}>{children}</Wrapper>;
}

Grid.Wrapper = function GridWrapper({ children, ...restProps }) {
  return <Wrapper {...restProps}>{children}</Wrapper>;
};

Grid.Header = function GridHeader({ children, ...restProps }) {
  return <Header {...restProps}>{children}</Header>;
};

Grid.Main = function GridMain({ children, ...restProps }) {
  return <Main {...restProps}>{children}</Main>;
};

Grid.Aside = function GridAside({ children, ...restProps }) {
  return <Aside {...restProps}>{children}</Aside>;
};

Grid.Footer = function GridFooter({ children, ...restProps }) {
  return <Footer {...restProps}>{children}</Footer>;
};
