import React, { useState, useContext, useEffect } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

import CommentsView from './commentsview';
import AwardUpdate from './awardupdate';

//SK
import { db } from '../context/firebase';

//SK Google Drive
import drive from '../social/drive.png';

import Grid from '../components/grid';
import { SidebarStories } from '../components/layout/sidebar-stories';
import { UniHeader } from '../containers/universilyheader';
import { FooterContainer } from '../containers/footer';

export default function AwardView() {
  return (
    <Grid>
      <Grid.Header>
        <UniHeader />
      </Grid.Header>
      <Grid.Main>
        <AwardViewPage />
      </Grid.Main>
      <Grid.Aside>
        <SidebarStories />
      </Grid.Aside>
      <Grid.Footer>
        <FooterContainer />
      </Grid.Footer>
    </Grid>
  );
}

function AwardViewPage() {
  const { firebase } = useContext(FirebaseContext);

  const [error, setError] = useState('');

  const [awards, setAwards] = useState([]);
  // SK
  const [userLocal] = useState(JSON.parse(localStorage.getItem('authUser')));

  firebase.auth().onAuthStateChanged(function (user) {
    if (user) {
      // User is signed in.
      var uid = user.uid;
      console.log('user is signed in for onAuthStateChanged: uid:' + uid);
    } else {
      // No user is signed in.
      console.log('user is NOT signed in for onAuthStateChanged.');
      var uid = '';
    }
  });

  useEffect(() => {
    db.collection('award')
      .where('createdBy', '==', userLocal.uid)
      .where('deleteDocument', '==', false)
      .get()
      .then((response) => {
        const fetchedAwards = [];
        response.docs.forEach((document) => {
          const fetchedAward = {
            id: document.id,
            ...document.data(),
          };
          fetchedAwards.push(fetchedAward);
        });
        setAwards(fetchedAwards);
      })
      .catch((error) => {
        setError(error);
      });
  }, []);

  return (
    <>
      <Form>
        <Form.SubTitle>View Awards</Form.SubTitle>
        {error && <Form.Error>{error}</Form.Error>}

        <Form.Table>
          <Form.Thead>
            <Form.Tr>
              <Form.Th>Type</Form.Th>
              <Form.Th>Name</Form.Th>
              <Form.Th>Scope</Form.Th>

              <Form.Th>Description</Form.Th>
              <Form.Th>Usage</Form.Th>
              <Form.Th>Updates</Form.Th>

              <Form.Th>Date</Form.Th>

              <Form.Th>Years</Form.Th>
              <Form.Th>Niche</Form.Th>
              <Form.Th>Review</Form.Th>
            </Form.Tr>
          </Form.Thead>
          <Form.Tbody>
            {error ? <p>Ops, there is an error :(</p> : null}
            {awards.map((award) => (
              <Form.Tr key={award.id}>
                <Form.Td> {award.awardType}</Form.Td>
                <Form.Td> {award.awardName}</Form.Td>
                <Form.Td> {award.awardScope}</Form.Td>

                <Form.Td> {award.description}</Form.Td>
                <Form.Td> {award.usage}</Form.Td>
                <Form.Td> {award.updates}</Form.Td>

                <Form.Td> {award.awardDate}</Form.Td>

                <Form.Td> {award.years}</Form.Td>
                <Form.Td> {award.niche}</Form.Td>

                <Form.Td> {award.review}</Form.Td>
              </Form.Tr>
            ))}
          </Form.Tbody>
        </Form.Table>

        <Form.Text>Want to add more Awards?</Form.Text>
        <Form.Link to="/award">Add Award</Form.Link>

        <Form.SubTitle>Awards Details</Form.SubTitle>

        <Form.Table>
          {awards.map((award) => (
            <Form.Tr key={award.id}>
              <Form.Tr>
                <Form.Th>Field</Form.Th>
                <Form.Th>Value</Form.Th>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>award Type</Form.Td>
                <Form.Td>{award.awardType}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>award Name</Form.Td>
                <Form.Td>{award.awardName}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>award Scope</Form.Td>
                <Form.Td>{award.awardScope}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Description</Form.Td>
                <Form.Td>{award.description}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Usage</Form.Td>
                <Form.Td>{award.usage}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Updates</Form.Td>
                <Form.Td>{award.updates}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>award Date</Form.Td>
                <Form.Td>{award.awardDate}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Years</Form.Td>
                <Form.Td>{award.years}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Niche</Form.Td>
                <Form.Td>{award.niche}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Review</Form.Td>
                <Form.Td>{award.review}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Drive Link</Form.Td>
                <Form.DriveLink driveLink={award.awardLink} drive={drive} />
              </Form.Tr>

              <Form.Text>
                <CommentsView
                  reviewee={userLocal.uid}
                  docID={award.id}
                  docType="award"
                />
              </Form.Text>

              <Form.Text>
                <AwardUpdate
                  createdBy={userLocal.uid}
                  docID={award.id}
                  docObject={award}
                />
              </Form.Text>
            </Form.Tr>
          ))}
        </Form.Table>
        <Form.Text>Want to add more Awards?</Form.Text>
        <Form.Link to="/award">Add Award</Form.Link>

        <Form.Link to="/menu-stories-view">Return to Stories</Form.Link>
        <Form.Link to="/browse">Return to Browse</Form.Link>

        <Form.TextSmall>You deserve your award!</Form.TextSmall>
      </Form>
    </>
  );
}
