import React, { useState, useContext, useEffect } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';
import CommentsView from './commentsview';
import NicheUpdate from './nicheupdate';
import { Content } from '../components/layout/content';

//SK
import { db } from '../context/firebase';

//SK Google Drive
import drive from '../social/drive.png';

import Grid from '../components/grid';
import { SidebarStories } from '../components/layout/sidebar-stories';
import { UniHeader } from '../containers/universilyheader';
import { FooterContainer } from '../containers/footer';

export default function NicheView() {
  return (
    <Grid>
      <Grid.Header>
        <UniHeader />
      </Grid.Header>
      <Grid.Main>
        <NicheViewPage />
      </Grid.Main>
      <Grid.Aside>
        <SidebarStories />
      </Grid.Aside>
      <Grid.Footer>
        <FooterContainer />
      </Grid.Footer>
    </Grid>
  );
}

export function NicheViewPage() {
  const { firebase } = useContext(FirebaseContext);

  const [error, setError] = useState('');
  //SK
  //var user = firebase.auth().currentUser;
  const [niches, setNiches] = useState([]);
  // SK
  const [userLocal] = useState(JSON.parse(localStorage.getItem('authUser')));

  useEffect(() => {
    db.collection('niche')
      .where('createdBy', '==', userLocal.uid)
      .where('deleteDocument', '==', false)
      .get()
      .then((response) => {
        const fetchedNiches = [];
        response.docs.forEach((document) => {
          const fetchedNiche = {
            id: document.id,
            ...document.data(),
          };
          fetchedNiches.push(fetchedNiche);
        });
        setNiches(fetchedNiches);
      })
      .catch((error) => {
        setError(error);
      });
  }, [userLocal.uid]);

  return (
    <>
      <Form>
        <Form.SubTitle>View Niches</Form.SubTitle>
        {error && <Form.Error>{error}</Form.Error>}
        <Form.Table>
          <Form.Thead>
            <Form.Tr>
              <Form.Th>Type</Form.Th>
              <Form.Th>Name</Form.Th>

              <Form.Th>Description</Form.Th>
              <Form.Th>Usage</Form.Th>
              <Form.Th>Updates</Form.Th>

              <Form.Th>Date</Form.Th>
            </Form.Tr>
          </Form.Thead>
          <Form.Tbody>
            {error ? <p>Ops, there is an error :(</p> : null}
            {niches.map((niche) => (
              <Form.Tr key={niche.id}>
                <Form.Td> {niche.nicheType}</Form.Td>
                <Form.Td> {niche.nicheName}</Form.Td>

                <Form.Td> {niche.description}</Form.Td>
                <Form.Td> {niche.usage}</Form.Td>
                <Form.Td> {niche.updates}</Form.Td>

                <Form.Td> {niche.nicheDate}</Form.Td>
              </Form.Tr>
            ))}
          </Form.Tbody>
        </Form.Table>
        <Form.Text>Want to add more Niches?</Form.Text>
        <Form.Link to="/niche">Add Niche</Form.Link>
        <Form.SubTitle>Niche Details</Form.SubTitle>
        <Form.Table>
          {niches.map((niche) => (
            <Form.Tr key={niche.id}>
              <Form.Tr>
                <Form.Th>Field</Form.Th>
                <Form.Th>Value</Form.Th>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Niche Type</Form.Td>
                <Form.Td>{niche.nicheType}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Niche Name</Form.Td>
                <Form.Td>{niche.nicheName}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Description</Form.Td>
                <Form.Td>{niche.description}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Usage</Form.Td>
                <Form.Td>{niche.usage}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Updates</Form.Td>
                <Form.Td>{niche.updates}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Niche Date</Form.Td>
                <Form.Td>{niche.nicheDate}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Drive Link</Form.Td>
                <Form.DriveLink driveLink={niche.nicheLink} drive={drive} />
              </Form.Tr>

              <Form.Text>
                <CommentsView
                  reviewee={userLocal.uid}
                  docID={niche.id}
                  docType="niche"
                />
              </Form.Text>

              <Form.Text>
                <NicheUpdate
                  createdBy={userLocal.uid}
                  docID={niche.id}
                  docObject={niche}
                />
              </Form.Text>
            </Form.Tr>
          ))}
        </Form.Table>
        Want to add another Niche?
        <Form.Link to="/niche">Add Niche</Form.Link>
        <Form.Link to="/menu-stories-view">Return to Stories</Form.Link>
        <Form.Link to="/browse">Return to Browse</Form.Link>
        <Form.TextSmall>You are in the Awesome Niche!</Form.TextSmall>
      </Form>
    </>
  );
}
