import React, { useState, useContext } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//SK
import { db } from '../context/firebase';

export default function CourseUpdate(props) {
  const { firebase } = useContext(FirebaseContext);

  const [error] = useState('');
  //SK
  var user = firebase.auth().currentUser;
  const [setLoader] = useState(false);

  //SK Set the docID passed from the link.
  //setDocumentID({ props.docID });
  //console.log('props:' + JSON.stringify(props));
  //console.log('props:props.docObject:  ' + JSON.stringify(props.docObject));
  //console.log('props: Update Niche: documentID:' + props.docID);

  // const [documentID, setDocumentID] = useState(props.docObject.ID);

  const [courseType, setCourseType] = useState(props.docObject.courseType);
  const [courseName, setCourseName] = useState(props.docObject.courseName);
  const [courseGrade, setCourseGrade] = useState(props.docObject.courseGrade);
  const [courseSemester, setCourseSemester] = useState(
    props.docObject.courseSemester
  );

  const [description, setDescription] = useState(props.docObject.description);

  const [usage] = useState(props.docObject.usage);
  const [updates] = useState(props.docObject.updates + 1);

  const [courseDate, setCourseDate] = useState(props.docObject.courseDate);
  const [courseLink, setCourseLink] = useState(props.docObject.courseLink);

  const [deleteDocument, setDeleteDocument] = useState(
    props.docObject.deleteDocument
  );

  const isInvalid = courseName === '';

  const handleCourseUpdate = (event) => {
    event.preventDefault();

    db.collection('course')
      .doc(props.docID)
      .set(
        {
          courseType: courseType,
          courseName: courseName,
          courseGrade: courseGrade,
          courseSemester: courseSemester,

          description: description,
          usage: usage,
          updates: updates,
          courseDate: courseDate,
          courseLink: courseLink,
          updatedAt: new Date(),
          updatedBy: user.uid,
          deleteDocument: deleteDocument,

          users: [
            {
              userId: user.uid,
            },
          ],
        },
        { merge: true }
      )
      .then(() => {
        setLoader(false);
        alert('Your course has been updated!');
      })
      .catch((error) => {
        alert(error.message);
        setLoader(false);
      });
  };

  return (
    <>
      <Form>
        <Form.SubTitle>Update Course</Form.SubTitle>
        {error && <Form.Error>{error}</Form.Error>}

        <Form.Base onSubmit={handleCourseUpdate} method="POST">
          <Form.Input
            placeholder="Course Type"
            data-lpignore="true"
            value={courseType}
            onChange={({ target }) => setCourseType(target.value)}
          />

          <Form.Input
            placeholder="Course Name"
            data-lpignore="true"
            value={courseName}
            onChange={({ target }) => setCourseName(target.value)}
          />

          <Form.Input
            placeholder="Course Grade"
            data-lpignore="true"
            value={courseGrade}
            onChange={({ target }) => setCourseGrade(target.value)}
          />
          <Form.Input
            placeholder="Course Semester"
            data-lpignore="true"
            value={courseSemester}
            onChange={({ target }) => setCourseSemester(target.value)}
          />

          <Form.Input
            placeholder="Description"
            data-lpignore="true"
            value={description}
            onChange={({ target }) => setDescription(target.value)}
          />
          <Form.Input
            placeholder="Course Date"
            type="date"
            value={courseDate}
            onChange={({ target }) => setCourseDate(target.value)}
          />
          <Form.Input
            placeholder="Course Link"
            data-lpignore="true"
            value={courseLink}
            onChange={({ target }) => setCourseLink(target.value)}
          />

          <label>
            <Form.Text>Delete Course?</Form.Text>
            <Form.Input
              placeholder="Delete Course"
              type="checkbox"
              checked={deleteDocument}
              onChange={({ target }) => setDeleteDocument(target.value)}
            />
          </label>

          <Form.Submit disabled={isInvalid} type="submit" data-testid="sign-up">
            Save Course
          </Form.Submit>
        </Form.Base>

        <Form.Text>
          Want to see Courses?{' '}
          <Form.Link to="/courseview">View Courses</Form.Link>
        </Form.Text>

        <Form.Link to="/menu-stories-view">Return to Stories</Form.Link>
        <Form.Link to="/browse">Return to Browse</Form.Link>

        <Form.TextSmall>You are so awesome!</Form.TextSmall>
      </Form>
    </>
  );
}
