import React, { useState, useContext } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//SK
import { db } from '../context/firebase';
import useMetadata from '../constants/metadata';


export default function ActivityUpdate(props) {
  const { firebase } = useContext(FirebaseContext);

  //SK Need to have activityTypes as var and double-quotes around "activitytypes".
  //var activityTypes  = useContent("activitytypes");
  var activityTypes = useMetadata("activityType");
  var activityScopes = useMetadata("activityScope");
  var activityStatuses = useMetadata("status");
  var activityReviews = useMetadata("review");
  //var activityReviewers = useMetadata("reviewer");


  console.log ('props.docID:' + props.docID);
  var activityDocID = props.docID;
  console.log ('activityDocID:' + activityDocID);


  const [error] = useState('');
  //SK
  var user = firebase.auth().currentUser;
  const [setLoader] = useState(false);

  const [activityType, setActivityType] = useState(props.docObject.activityType);
  const [activityName, setActivityName] = useState(props.docObject.activityName);
  const [description, setDescription] = useState(props.docObject.description);
  const [activityScope, setActivityScope] = useState(props.docObject.activityScope);

  const [startDate, setStartDate] = useState(props.docObject.startDate);

  const [status, setStatus] = useState(props.docObject.status);
  const [review, setReview] = useState(props.docObject.review);

  const [isActivity, setIsActivity] = useState(props.docObject.isActivity);
  const [isHonor, setIsHonor] = useState(props.docObject.isHonor);
  const [isAdditional, setIsAdditional] = useState(props.docObject.isAdditional);

  const [activityLink, setActivityLink] = useState(props.docObject.activityLink);

  //SK Removed for update of array. 
  const [reviewers, setReviewers] = useState(props.docObject.reviewers);


  const [updates] = useState(props.docObject.updates + 1);
  const [deleteDocument, setDeleteDocument] = useState(false);

  const isInvalid = props.docID === '';

  const handleActivityUpdate = (event) => {
    event.preventDefault();

    db.collection('activity')
      .doc(activityDocID)
      .set(
        {
        activityType: activityType,
        activityName: activityName,
        description: description,
        activityScope: activityScope,
        startDate: startDate,
        activityLink: activityLink,
        status: status,
        review: review,
        isActivity: isActivity,
        isHonor: isHonor,
        isAdditional: isAdditional,
        updatedAt: new Date(),
        updatedBy: user.uid,
        deleteDocument: deleteDocument,

          users: [
            {
              userId: user.uid,
            },
          ],
        },
        { merge: true }
      )
      .then(() => {
        setLoader(false);
        alert('Your activity has been updated!');
      })
      .catch((error) => {
        alert(error.message);
        setLoader(false);
      });
  };

  return (
    <>
      <Form>
        <Form.Title>Update Activity</Form.Title>
        {error && <Form.Error>{error}</Form.Error>}

        <Form.Base onSubmit={handleActivityUpdate} method="POST">

        <label>
            <Form.Text>Choose Activity Type:</Form.Text>
            <select
              value={activityType}
              data-lpignore="true"
              onChange={({ target }) => setActivityType(target.value)}
            >
{' '}
{activityTypes.map(type => (<option value={type.code}>{type.type}</option>))}
             

            </select>
          </label>

          <Form.Input
            placeholder="Activity Name (Required)"
            value={activityName}
            type="search"
            data-lpignore="true"
            onChange={({ target }) => setActivityName(target.value)}
          />
          <Form.InputLong
            placeholder="Description"
            type="search"
            value={description}
            data-lpignore="true"
            onChange={({ target }) => setDescription(target.value)}
          />

          <Form.Input
            placeholder="Start Date"
            type="date"
            value={startDate}
            data-lpignore="true"
            onChange={({ target }) => setStartDate(target.value)}
          />

          <label>
            <Form.Text>Choose Scope:</Form.Text>
            <select
              value={activityScope}
              data-lpignore="true"
              onChange={({ target }) => setActivityScope(target.value)}
            >
              {' '}
              {activityScopes.map(type => (<option value={type.code}>{type.type}</option>))}

            </select>
          </label>

          <Form.Table>
            <Form.Tr>
              <Form.Td>
                <label>
                  <Form.Text>Is Activity:</Form.Text>
                  <Form.Input
                    placeholder="Is Activity"
                    type="checkbox"
                    checked={isActivity}
                    onChange={({ target }) => setIsActivity(target.value)}
                  />
                </label>
              </Form.Td>
              <Form.Td>
                <label>
                  <Form.Text>Is Honor:</Form.Text>
                  <Form.Input
                    placeholder="Is Honor"
                    type="checkbox"
                    checked={isHonor}
                    onChange={({ target }) => setIsHonor(target.value)}
                  />
                </label>
              </Form.Td>
              <Form.Td>
                <label>
                  <Form.Text>Is Additional Info:</Form.Text>
                  <Form.Input
                    placeholder="Is Additional Info"
                    type="checkbox"
                    checked={isAdditional}
                    onChange={({ target }) => setIsAdditional(target.value)}
                  />
                </label>
              </Form.Td>
            </Form.Tr>
          </Form.Table>

          <Form.Table>
            <Form.Tr>
              <Form.Td>
                <label>
                  <Form.Text>Choose Status:</Form.Text>
                  <select
                    value={status}
                    onChange={({ target }) => setStatus(target.value)}
                  >
                    {' '}
                    {activityStatuses.map(type => (<option value={type.code}>{type.type}</option>))}
                  </select>
                </label>
              </Form.Td>
              <Form.Td>
                <label>
                  <Form.Text>Choose Review:</Form.Text>
                  <select
                    value={review}
                    onChange={({ target }) => setReview(target.value)}
                  >
                    {' '}
                    {activityReviews.map(type => (<option value={type.code}>{type.type}</option>))}
                  </select>
                </label>
              </Form.Td>
            </Form.Tr>
          </Form.Table>

          <Form.Input
            placeholder="Link: (example:) https://drive.google.com/drive/my-drive"
            value={activityLink}
            data-lpignore="true"
            onChange={({ target }) => setActivityLink(target.value)}
          />

          <label>
            <Form.Text>Delete Activity?</Form.Text>
            <Form.Input
              placeholder="Delete Activity"
              type="checkbox"
              checked={deleteDocument}
              onChange={({ target }) => setDeleteDocument(target.value)}
            />
          </label>

          <Form.Submit
            disabled={isInvalid}
            type="submit"
            data-testid="update-activity"
          >
            Save Activity Update
          </Form.Submit>
        </Form.Base>

        <Form.Text>
          Want to see Activities?{' '}
          <Form.Link to="/activityview">View Activities</Form.Link>
        </Form.Text>
        <Form.Link to="/menu-stories-view">Return to Stories</Form.Link>
        <Form.Link to="/browse">Return to Browse</Form.Link>
        <Form.TextSmall>You are so awesome!</Form.TextSmall>
      </Form>
    </>
  );
}
