import React, { useState, useContext, useEffect } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//SK
import { db } from '../context/firebase';

export default function ActivityViewSummary() {
  const { firebase } = useContext(FirebaseContext);

  const [error, setError] = useState('');
  //SK
  const [activities, setActivities] = useState([]);
  const [userLocal, setUserLocal] = useState(
    JSON.parse(localStorage.getItem('authUser'))
  );

  useEffect(() => {
    db.collection('activity')
      .where('createdBy', '==', userLocal.uid)
      .where('deleteDocument', '==', false)
      .get()
      .then((response) => {
        const fetchedActivities = [];
        response.docs.forEach((document) => {
          const fetchedActivity = {
            id: document.id,
            ...document.data(),
          };
          fetchedActivities.push(fetchedActivity);
        });
        setActivities(fetchedActivities);
      })
      .catch((error) => {
        setError(error);
      });
  }, []);

  return (
    <>
      <Form.SubTitle>View Activities</Form.SubTitle>
      {error && <Form.Error>{error}</Form.Error>}
      <Form.Table>
        <Form.Thead>
          <Form.Tr>
            <Form.Th>Name</Form.Th>
            <Form.Th>Description</Form.Th>
            <Form.Th>Scope</Form.Th>
            <Form.Th>Honor</Form.Th>
            <Form.Th>Additional Info</Form.Th>
            <Form.Th>Status</Form.Th>
            <Form.Th>Review</Form.Th>
          </Form.Tr>
        </Form.Thead>

        {error ? <p>Ops, there is an error :(</p> : null}
        {activities.map((activity) => (
          <Form.Tr key={activity.id}>
            <Form.Td> {activity.activityName}</Form.Td>
            <Form.Td> {activity.description}</Form.Td>
            <Form.Td> {activity.activityScope}</Form.Td>
            <Form.Td> {activity.isActivity}</Form.Td>
            <Form.Td> {activity.isHonor}</Form.Td>
            <Form.Td> {activity.isAdditional}</Form.Td>
            <Form.Td> {activity.status}</Form.Td>
            <Form.Td> {activity.review}</Form.Td>
          </Form.Tr>
        ))}
      </Form.Table>
    </>
  );
}
