import React, { useState, useContext, useEffect } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';
import CommentsView from './commentsview';
import CourseUpdate from './courseupdate';
import { Content } from '../components/layout/content';

//SK
import { db } from '../context/firebase';

//SK Google Drive
import drive from '../social/drive.png';

import Grid from '../components/grid';
import { SidebarStories } from '../components/layout/sidebar-stories';
import { UniHeader } from '../containers/universilyheader';
import { FooterContainer } from '../containers/footer';

export default function CourseView() {
  return (
    <Grid>
      <Grid.Header>
        <UniHeader />
      </Grid.Header>
      <Grid.Main>
        <CourseViewPage />
      </Grid.Main>
      <Grid.Aside>
        <SidebarStories />
      </Grid.Aside>
      <Grid.Footer>
        <FooterContainer />
      </Grid.Footer>
    </Grid>
  );
}

export function CourseViewPage() {
  const { firebase } = useContext(FirebaseContext);

  const [error, setError] = useState('');
  //SK
  //var user = firebase.auth().currentUser;
  const [courses, setCourses] = useState([]);
  // SK
  const [userLocal] = useState(JSON.parse(localStorage.getItem('authUser')));

  firebase.auth().onAuthStateChanged(function (user) {
    if (user) {
      // User is signed in.
      //var uid = user.uid;
      //console.log('user is signed in for onAuthStateChanged: uid');
    } else {
      // No user is signed in.
      //console.log('user is NOT signed in for onAuthStateChanged.');
      //var uid = '';
    }
  });

  //       .where('createdBy', '==', user.uid)
  //       .where('createdBy', '==', uid)
  //      .where('createdBy', '==', userLocal.uid)

  useEffect(() => {
    db.collection('course')
      .where('createdBy', '==', userLocal.uid)
      .where('deleteDocument', '==', false)
      .get()
      .then((response) => {
        const fetchedCourses = [];
        response.docs.forEach((document) => {
          const fetchedCourse = {
            id: document.id,
            ...document.data(),
          };
          fetchedCourses.push(fetchedCourse);
        });
        setCourses(fetchedCourses);
      })
      .catch((error) => {
        setError(error);
      });
  }, [userLocal.uid]);

  return (
    <>
      <Form>
        <Form.SubTitle>View Courses</Form.SubTitle>
        {error && <Form.Error>{error}</Form.Error>}
        <Form.Table>
          <Form.Thead>
            <Form.Tr>
              <Form.Th>Type</Form.Th>
              <Form.Th>Name</Form.Th>
              <Form.Th>Grade</Form.Th>
              <Form.Th>Semester</Form.Th>

              <Form.Th>Description</Form.Th>
              <Form.Th>Usage</Form.Th>
              <Form.Th>Updates</Form.Th>

              <Form.Th>Date</Form.Th>
            </Form.Tr>
          </Form.Thead>
          <Form.Tbody>
            {error ? <p>Ops, there is an error :(</p> : null}
            {courses.map((course) => (
              <Form.Tr key={course.id}>
                <Form.Td> {course.courseType}</Form.Td>
                <Form.Td> {course.courseName}</Form.Td>
                <Form.Td> {course.courseGrade}</Form.Td>
                <Form.Td> {course.courseSemester}</Form.Td>

                <Form.Td> {course.description}</Form.Td>
                <Form.Td> {course.usage}</Form.Td>
                <Form.Td> {course.updates}</Form.Td>

                <Form.Td> {course.courseDate}</Form.Td>
              </Form.Tr>
            ))}
          </Form.Tbody>
        </Form.Table>
        Want to add more Courses?
        <Form.Link to="/course">Add Course</Form.Link>
        <Form.SubTitle>Course Details</Form.SubTitle>
        <Form.Table>
          {courses.map((course) => (
            <Form.Tr key={course.id}>
              <Form.Tr>
                <Form.Th>Field</Form.Th>
                <Form.Th>Value</Form.Th>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Course Type</Form.Td>
                <Form.Td>{course.courseType}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Course Name</Form.Td>
                <Form.Td>{course.courseName}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Course Grade</Form.Td>
                <Form.Td>{course.courseGrade}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Course Semester</Form.Td>
                <Form.Td>{course.CourseSemester}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Description</Form.Td>
                <Form.Td>{course.description}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Usage</Form.Td>
                <Form.Td>{course.usage}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Updates</Form.Td>
                <Form.Td>{course.updates}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Course Date</Form.Td>
                <Form.Td>{course.courseDate}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Drive Link</Form.Td>
                <Form.DriveLink driveLink={course.courseLink} drive={drive} />
              </Form.Tr>

              <Form.Text>
                <CommentsView
                  reviewee={userLocal.uid}
                  docID={course.id}
                  docType="course"
                />
              </Form.Text>

              <Form.Text>
                <CourseUpdate
                  createdBy={userLocal.uid}
                  docID={course.id}
                  docObject={course}
                />
              </Form.Text>
            </Form.Tr>
          ))}
        </Form.Table>
        Want to add another Course?
        <Form.Link to="/course">Add Course</Form.Link>
        <Form.Link to="/menu-stories-view">Return to Stories</Form.Link>
        <Form.Link to="/browse">Return to Browse</Form.Link>
        <Form.TextSmall>You are taking the Awesome Courses!</Form.TextSmall>
      </Form>
    </>
  );
}
