import React, { useState, useContext, useEffect } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';
import CommentsView from './commentsview';
import TestingUpdate from './testingupdate';

//SK
import { db } from '../context/firebase';

//SK Google Drive
import drive from '../social/drive.png';

import Grid from '../components/grid';
import { SidebarStories } from '../components/layout/sidebar-stories';
import { UniHeader } from '../containers/universilyheader';
import { FooterContainer } from '../containers/footer';

export default function TestingView() {
  return (
    <Grid>
      <Grid.Header>
        <UniHeader />
      </Grid.Header>
      <Grid.Main>
        <TestingViewPage />
      </Grid.Main>
      <Grid.Aside>
        <SidebarStories />
      </Grid.Aside>
      <Grid.Footer>
        <FooterContainer />
      </Grid.Footer>
    </Grid>
  );
}

export function TestingViewPage() {
  const { firebase } = useContext(FirebaseContext);

  const [error, setError] = useState('');
  //SK
  //var user = firebase.auth().currentUser;
  const [testings, setTestings] = useState([]);
  // SK
  const [userLocal] = useState(JSON.parse(localStorage.getItem('authUser')));

  firebase.auth().onAuthStateChanged(function (user) {
    if (user) {
      // User is signed in.
      //var uid = user.uid;
      //console.log('user is signed in for onAuthStateChanged: uid');
    } else {
      // No user is signed in.
      //console.log('user is NOT signed in for onAuthStateChanged.');
      //var uid = '';
    }
  });

  //       .where('createdBy', '==', user.uid)
  //       .where('createdBy', '==', uid)
  //      .where('createdBy', '==', userLocal.uid)

  useEffect(() => {
    db.collection('testing')
      .where('createdBy', '==', userLocal.uid)
      .get()
      .then((response) => {
        const fetchedTestings = [];
        response.docs.forEach((document) => {
          const fetchedTesting = {
            id: document.id,
            ...document.data(),
          };
          fetchedTestings.push(fetchedTesting);
        });
        setTestings(fetchedTestings);
      })
      .catch((error) => {
        setError(error);
      });
  }, [userLocal.uid]);

  return (
    <>
      <Form>
        <Form.Title>View Testings</Form.Title>
        {error && <Form.Error>{error}</Form.Error>}
        <Form.Table>
          <Form.Thead>
            <Form.Tr>
              <Form.Th>Type</Form.Th>
              <Form.Th>Name</Form.Th>

              <Form.Th>Description</Form.Th>
              <Form.Th>Usage</Form.Th>
              <Form.Th>Updates</Form.Th>

              <Form.Th>Date</Form.Th>
              <Form.Th>Status</Form.Th>
            </Form.Tr>
          </Form.Thead>
          <Form.Tbody>
            {error ? <p>Ops, there is an error :(</p> : null}
            {testings.map((testing) => (
              <Form.Tr key={testing.id}>
                <Form.Td> {testing.testType}</Form.Td>
                <Form.Td> {testing.testName}</Form.Td>

                <Form.Td> {testing.description}</Form.Td>
                <Form.Td> {testing.usage}</Form.Td>
                <Form.Td> {testing.updates}</Form.Td>

                <Form.Td> {testing.testDate}</Form.Td>
                <Form.Td> {testing.status}</Form.Td>
              </Form.Tr>
            ))}
          </Form.Tbody>
        </Form.Table>
        Want to add more Testings?
        <Form.Link to="/testing">Add Testing</Form.Link>
        <Form.Title>Testing Details</Form.Title>
        <Form.Table>
          {testings.map((testing) => (
            <Form.Tr key={testing.id}>
              <Form.Tr>
                <Form.Th>Field</Form.Th>
                <Form.Th>Value</Form.Th>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Testing Type</Form.Td>
                <Form.Td>{testing.testType}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Test Name</Form.Td>
                <Form.Td>{testing.testName}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Description</Form.Td>
                <Form.Td>{testing.description}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Usage</Form.Td>
                <Form.Td>{testing.usage}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Updates</Form.Td>
                <Form.Td>{testing.updates}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Testing Date</Form.Td>
                <Form.Td>{testing.testDate}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Drive Link</Form.Td>
                <Form.DriveLink driveLink={testing.testLink} drive={drive} />
              </Form.Tr>

              <Form.Text>
                <CommentsView
                  reviewee={userLocal.uid}
                  docID={testing.id}
                  docType="testing"
                />
              </Form.Text>

              <Form.Text>
                <TestingUpdate
                  createdBy={userLocal.uid}
                  docID={testing.id}
                  docObject={testing}
                />
              </Form.Text>
            </Form.Tr>
          ))}
        </Form.Table>
        Want to add another Testing?
        <Form.Link to="/testing">Add Testing</Form.Link>
        <Form.Link to="/browse">Return to Browse</Form.Link>
        <Form.TextSmall>You passed the Test!</Form.TextSmall>
      </Form>
    </>
  );
}
