import React from 'react';
import { BrowserRouter as Router, Switch } from 'react-router-dom';
import {
  Home,
  Browse,
  Journey,
  MenuStoriesView,
  MenuApplicationsView,
  SignIn,
  SignUp,
  Activity,
  ActivityView,
  ActivityReview,
  Honor,
  HonorView,
  Comment,
  CommentsView,
  Award,
  AwardView,
  Demographic,
  DemographicView,
  Essay,
  EssayView,
  Niche,
  NicheView,
  Course,
  CourseView,
  Testing,
  TestingView,
  Profile,
  ProfileView,
  Parent,
  ParentView,
  Social,
  Admin,
  AdminView,
} from './pages';
import * as ROUTES from './constants/routes';
import { IsUserRedirect, ProtectedRoute } from './helpers/routes';
import { useAuthListener } from './hooks';

import ForgotPassword from './pages/forgotPassword';
// SK Handle refresh with React Routes
//const Page404 = ({ location }) => (
// <div>
//    <h2>
//      No match found for <code>{location.pathname}</code>
//    </h2>
//  </div>
//);

export function App() {
  const { user } = useAuthListener();

  return (
    <Router>
      <Switch>
        <IsUserRedirect
          user={user}
          loggedInPath={ROUTES.BROWSE}
          path={ROUTES.SIGN_IN}
        >
          <SignIn />
        </IsUserRedirect>

        <IsUserRedirect
          user={user}
          loggedInPath={ROUTES.BROWSE}
          path={ROUTES.SIGN_UP}
        >
          <SignUp />
        </IsUserRedirect>
          <IsUserRedirect
          user={user}
          loggedInPath={ROUTES.BROWSE}
          path={ROUTES.FORGOT_PASSWORD}
        >
          <ForgotPassword />
        </IsUserRedirect>

        <ProtectedRoute user={user} path={ROUTES.BROWSE}>
          <Browse />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.JOURNEY}>
          <Journey />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.MENUSTORIESVIEW}>
          <MenuStoriesView />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.MENUAPPLICATIONSVIEW}>
          <MenuApplicationsView />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.ACTIVITY}>
          <Activity />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.ACTIVITYVIEW}>
          <ActivityView />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.ACTIVITYREVIEW}>
          <ActivityReview />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.HONOR}>
          <Honor />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.HONORVIEW}>
          <HonorView />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.COMMENT}>
          <Comment />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.COMMENTSVIEW}>
          <CommentsView />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.AWARD}>
          <Award />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.AWARDVIEW}>
          <AwardView />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.DEMOGRAPHIC}>
          <Demographic />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.DEMOGRAPHICVIEW}>
          <DemographicView />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.ESSAY}>
          <Essay />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.ESSAYVIEW}>
          <EssayView />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.NICHE}>
          <Niche />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.NICHEVIEW}>
          <NicheView />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.COURSE}>
          <Course />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.COURSEVIEW}>
          <CourseView />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.TESTING}>
          <Testing />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.TESTINGVIEW}>
          <TestingView />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.PROFILE}>
          <Profile />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.PROFILEVIEW}>
          <ProfileView />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.PARENT}>
          <Parent />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.PARENTVIEW}>
          <ParentView />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.SOCIAL}>
          <Social />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.ADMIN}>
          <Admin />
        </ProtectedRoute>

        <ProtectedRoute user={user} path={ROUTES.ADMINVIEW}>
          <AdminView />
        </ProtectedRoute>

        <IsUserRedirect
          user={user}
          loggedInPath={ROUTES.BROWSE}
          path={ROUTES.HOME}
        >
          <Home />
        </IsUserRedirect>
      </Switch>
    </Router>
  );
}
