import React, { useState, useContext } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//SK
import { db } from '../context/firebase';

export default function Profile() {
  const { firebase } = useContext(FirebaseContext);

  const [error] = useState('');
  //SK
  var user = firebase.auth().currentUser;
  const [setLoader] = useState(false);

  const [profileType, setProfileType] = useState('None');
  const [profileName, setProfileName] = useState('');
  const [description, setDescription] = useState('');
  const [address, setAddress] = useState('');
  const [phone, setPhone] = useState('');

  const [profileDate, setProfileDate] = useState('');
  const [profileLink, setProfileLink] = useState('');

  const [deleteDocument, setDeleteDocument] = useState(false);

  const isInvalid = profileName === '';

  const handleProfile = (event) => {
    event.preventDefault();

    //        createdAt: firebase.firestore.FieldValue.serverTimestamp(),

    db.collection('profile')
      .add({
        uid: user.uid,
        profileType: profileType,
        profileName: profileName,
        description: description,
        address: address,
        phone: phone,
        profileDate: profileDate,
        profileLink: profileLink,
        deleteDocument: deleteDocument,
        createdAt: new Date(),
        createdBy: user.uid,
        users: [
          {
            userId: user.uid,
          },
        ],
      })
      .then(() => {
        // setLoader(false);
        alert('Your profile has been saved!');
      })
      .catch((error) => {
        alert(error.message);
        // setLoader(false);
      });
    setProfileType('None');
    setProfileName('');

    setDescription('');
    setAddress('');
    setPhone('');

    setProfileDate('');
    setProfileLink('');

    setDeleteDocument(false);
  };

  return (
    <>
      <Form>
        <Form.Title>Update Profile</Form.Title>
        {error && <Form.Error>{error}</Form.Error>}

        <Form.Base onSubmit={handleProfile} method="POST">
          <label>
            <Form.Text>Choose Profile Type:</Form.Text>
            <select
              value={profileType}
              onChange={({ target }) => setProfileType(target.value)}
            >
              {' '}
              <option value="Student">Student</option>
              <option value="Advisor">Advisor</option>
              <option value="Counselor">Counselor</option>
              <option value="Parent">Parent</option>
              <option value="Reviewer">Reviewer </option>
              <option value="Other">Other</option>
              <option value="None">None</option>
            </select>
          </label>

          <Form.Input
            placeholder="Profile Name (Your Full Name) *Required*"
            data-lpignore="true"
            value={profileName}
            onChange={({ target }) => setProfileName(target.value)}
          />

          <Form.Input
            placeholder="Description"
            data-lpignore="true"
            type="textarea"
            value={description}
            onChange={({ target }) => setDescription(target.value)}
          />

          <Form.Input
            placeholder="Full Address (Street, City, State, Zip)"
            data-lpignore="true"
            type={Text}
            value={address}
            onChange={({ target }) => setAddress(target.value)}
          />

          <Form.Input
            placeholder="Full Phone (+1-888-555-1212, Country, Area, Phone)"
            data-lpignore="true"
            type={Text}
            value={phone}
            onChange={({ target }) => setPhone(target.value)}
          />

          <Form.Input
            placeholder="Profile Date"
            type="date"
            value={profileDate}
            onChange={({ target }) => setProfileDate(target.value)}
          />
          <Form.Input
            placeholder="Profile Link"
            data-lpignore="true"
            value={profileLink}
            onChange={({ target }) => setProfileLink(target.value)}
          />
          <Form.Submit disabled={isInvalid} type="submit" data-testid="sign-up">
            Save Profile
          </Form.Submit>
        </Form.Base>

        <Form.Text>
          Want to see your Profile?{' '}
          <Form.Link to="/profileview">View Profile</Form.Link>
        </Form.Text>
        <Form.Link to="/browse">Return to Browse</Form.Link>
        <Form.TextSmall>You are so awesome!</Form.TextSmall>
      </Form>
    </>
  );
}
