// import { FooterContainer } from '../containers/footer';
import * as ROUTES from '../constants/routes';
import {
  LoginMain,
  LoginForm,
  LogoBlock,
  LoginTitleBlock,
  CustomForm,
  BtnCommon,
  LastLink,
  ErrorMessage,
} from '../components/signup/styles/signup';

export default function SignIn() {
  const useStyles = makeStyles((theme) => ({
    formControl: {
      margin: theme.spacing(1),
      minWidth: 120,
    },
    selectEmpty: {
      marginTop: theme.spacing(2),
    },
    root: {
      '& .MuiTextField-root': {
        margin: theme.spacing(1),
        width: '25ch',
      },
    },
  }));

  const classes = useStyles();
  const history = useHistory();
  const [checkBoxs, setCheckBoxs] = React.useState({
    checkedA: true,
    checkedB: true,
    checkedF: true,
    checkedG: true,
  });

  const handleChackBoxChange = (event) => {
    setCheckBoxs({ ...checkBoxs, [event.target.name]: event.target.checked });
  };
  const { firebase } = useContext(FirebaseContext);

  const [emailAddress, setEmailAddress] = useState('');
  const [password, setPassword] = useState('');
  const [firstName, setFirstName] = useState('');

  const [error, setError] = useState('');
  const [age, setAge] = useState('');

  const isInvalid = password === '' || emailAddress === '';

  const handleSignup = (event) => {
    event.preventDefault();

    return firebase
      .auth()
      .createUserWithEmailAndPassword(emailAddress, password)
      .then((result) =>
        result.user
          .updateProfile({
            displayName: firstName,
            photoURL: Math.floor(Math.random() * 5) + 1,
          })
          .then(() => {
            history.push(ROUTES.BROWSE);
            // Verify Email
            firebase.auth().currentUser.sendEmailVerification();
          })
      )
      .catch((error) => {
        setFirstName('');
        setEmailAddress('');
        setPassword('');
        setError(error.message);
      });
  };

  const handleChange = (event) => {
    const { name } = event.target;
    setAge(name);
  };

  return (
    <>
      <LoginMain>
        <LoginForm>
          <LogoBlock>
            <a href="#">
              {/* <img src="../images/logo.svg></img> */}
              <img src="/images/logo.svg" alt="logo" />
            </a>
          </LogoBlock>
          <LoginTitleBlock>
            <h2>Register</h2>
            <FormControl className={classes.formControl}>
              <NativeSelect
                // value={age}
                onChange={handleChange}
                name="age"
                className={classes.selectEmpty}
                inputProps={{ 'aria-label': 'age' }}
              >
                <option value="">English</option>
                <option value={10}>English</option>
                <option value={20}>English</option>
                <option value={30}>English</option>
              </NativeSelect>
            </FormControl>
          </LoginTitleBlock>
          <CustomForm
            className="register-form"
            onSubmit={handleSignup}
            method="POST"
          >
            <TextField
              id="outlined-helperText"
              label="First Name"
              // defaultValue="Email"
              type="text"
              // helperText="Some important text"
              variant="outlined"
              value={firstName}
              onChange={({ target }) => setFirstName(target.value)}
            />
            <TextField
              id="outlined-helperText"
              label="Email"
              // defaultValue="Email"
              type="email"
              // helperText="Some important text"
              variant="outlined"
              value={emailAddress}
              onChange={({ target }) => setEmailAddress(target.value)}
            />
            <TextField
              id="outlined-helperText"
              label="Password"
              type="password"
              value={password}
              // helperText="Some important text"
              variant="outlined"
              placeholder="Password"
              onChange={({ target }) => setPassword(target.value)}
            />
            <ErrorMessage>{error}</ErrorMessage>
            <BtnCommon className="register-btn">
              <Button variant="contained" color="primary" disabled={isInvalid} type="submit">
                Register
              </Button>
            </BtnCommon>
            <LastLink>
              <p>
                Already a user? <Link to={ROUTES.SIGN_IN}>Sign in now</Link>.
              </p>
            </LastLink>
          </CustomForm>
        </LoginForm>
      </LoginMain>
      {/* <HeaderContainer> */}

      {/* <Form>
          <Form.Title>Sign In</Form.Title>
          {error && <Form.Error data-testid="error">{error}</Form.Error>}

          <Form.Base onSubmit={handleSignin} method="POST">
            <Form.Input
              placeholder="Email address"
              value={emailAddress}
              onChange={({ target }) => setEmailAddress(target.value)}
            />
            <Form.Input
              type="password"
              value={password}
              autoComplete="off"
              placeholder="Password"
              onChange={({ target }) => setPassword(target.value)}
            />
            <Form.Submit
              disabled={isInvalid}
              type="submit"
              data-testid="sign-in"
            >
              Sign In
            </Form.Submit>
          </Form.Base>

          <Form.Text>
            New to Universily? <Form.Link to="/signup">Sign up now.</Form.Link>
          </Form.Text>
          <Form.TextSmall>
            This page is protected by Google reCAPTCHA to ensure you're not a
            bot. Learn more.
          </Form.TextSmall>
        </Form> */}
      {/* </HeaderContainer> */}
      {/* <FooterContainer /> */}
    </>
  );
}
