import React, { useState, useContext } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//SK
import { db } from '../context/firebase';

//SK accepts props.
export default function Comment(props) {
  const { firebase } = useContext(FirebaseContext);

  const [error] = useState('');
  //SK
  var user = firebase.auth().currentUser;
  const [setLoader] = useState(false);

  //SK Set the docID passed from the link.
  //setDocumentID({ props.docID });
  //console.log('props:' + JSON.stringify(props));
  //console.log('props: documentID:' + props.docID);

  const [documentID, setDocumentID] = useState(props.docID);
  const [commentType, setCommentType] = useState(props.docType);
  const [commentName, setCommentName] = useState('');
  const [description, setDescription] = useState('');
  const [status, setStatus] = useState('New');

  const [commentDate, setCommentDate] = useState('');
  const [commentLink, setCommentLink] = useState('');
  const [reviewee, setReviewee] = useState(props.reviewee);
  const [reviewers, setReviewers] = useState('');

  const isInvalid = commentName === '';

  const handleComment = (event) => {
    event.preventDefault();

    //        createdAt: firebase.firestore.FieldValue.serverTimestamp(),

    db.collection('comment')
      .add({
        docID: documentID,
        commentType: commentType,
        commentName: commentName,
        description: description,
        status: status,
        commentDate: new Date(),
        commentLink: commentLink,
        createdAt: new Date(),
        createdBy: user.uid,
        reviewee: reviewee,
        reviewers: user.uid,
        users: [
          {
            userId: user.uid,
          },
        ],
      })
      .then(() => {
        setLoader(false);
        alert('Your comment has been saved!');
      })
      .catch((error) => {
        alert(error.message);
        setLoader(false);
      });

    setDocumentID('');

    setCommentType('');
    setCommentName('');
    setDescription('');
    setStatus('');

    setReviewee('');
    setReviewers('');

    setCommentDate('');
    setCommentLink('');
  };

  return (
    <>
      <Form>
        <Form.Text>Add Comment</Form.Text>
        {error && <Form.Error>{error}</Form.Error>}

        <Form.Base onSubmit={handleComment} method="POST">
          <Form.Input
            placeholder="Comment Name"
            type={Text}
            value={commentName}
            onChange={({ target }) => setCommentName(target.value)}
          />
          <Form.InputLong
            placeholder="Description"
            type="textarea"
            value={description}
            onChange={({ target }) => setDescription(target.value)}
          />

          <Form.Input
            placeholder="Comment Link"
            type={URL}
            value={commentLink}
            onChange={({ target }) => setCommentLink(target.value)}
          />

          <Form.Submit disabled={isInvalid} type="submit" data-testid="sign-up">
            Save Comment
          </Form.Submit>
        </Form.Base>

        <Form.TextSmall>Your comments are so awesome!</Form.TextSmall>
      </Form>
    </>
  );
}
