export function ProfileViewPage() {
  const { firebase } = useContext(FirebaseContext);

  const [error, setError] = useState('');

  //SK Trying to refresh list of reviewers after adding a new one.
  //const [refresh, setRefresh] = useState(0);
  //console.log('refresh:' + refresh);

  //SK
  //var user = firebase.auth().currentUser;
  const [profiles, setProfiles] = useState([]);
  // SK
  const [userLocal, setUserLocal] = useState(
    JSON.parse(localStorage.getItem('authUser'))
  );

  firebase.auth().onAuthStateChanged(function (user) {
    if (user) {
      // User is signed in.
      var uid = user.uid;
      console.log('user is signed in for onAuthStateChanged: uid:' + uid);
    } else {
      // No user is signed in.
      console.log('user is NOT signed in for onAuthStateChanged.');
      var uid = '';
    }
  });

  //       .where('createdBy', '==', user.uid)
  //       .where('createdBy', '==', uid)
  //      .where('createdBy', '==', userLocal.uid)

  useEffect(() => {
    db.collection('profile')
      .where('uid', '==', userLocal.uid)
      .where('deleteDocument', '==', false)
      .get()
      .then((response) => {
        const fetchedProfiles = [];
        response.docs.forEach((document) => {
          const fetchedProfile = {
            id: document.id,
            ...document.data(),
          };
          fetchedProfiles.push(fetchedProfile);
        });
        setProfiles(fetchedProfiles);
      })
      .catch((error) => {
        setError(error);
      });
  }, []);

  return (
    <>
      <Form>
        <Form.Title>View Profile</Form.Title>
        {error && <Form.Error>{error}</Form.Error>}
        <Form.Table>
          <Form.Thead>
            <Form.Tr>
              <Form.Th>Type</Form.Th>
              <Form.Th>Name</Form.Th>
              <Form.Th>Email</Form.Th>
              <Form.Th>Phone</Form.Th>

              <Form.Th>Description</Form.Th>
              <Form.Th>Usage</Form.Th>

              <Form.Th>Date</Form.Th>
            </Form.Tr>
          </Form.Thead>
          <Form.Tbody>
            {error ? <p>Ops, there is an error :(</p> : null}
            {profiles.map((profile) => (
              <Form.Tr key={profile.id}>
                <Form.Td> {profile.profileType}</Form.Td>
                <Form.Td> {profile.profileName}</Form.Td>
                <Form.Td> {userLocal.email}</Form.Td>
                <Form.Td> {profile.phone}</Form.Td>

                <Form.Td> {profile.description}</Form.Td>
                <Form.Td> {profile.address}</Form.Td>

                <Form.Td> {profile.profileDate}</Form.Td>
              </Form.Tr>
            ))}
          </Form.Tbody>
        </Form.Table>
        <Form.Link to="/profileview">View Profile</Form.Link>
        <Form.Link to="/profile">Add Profile</Form.Link>
        <Form.Title>Profile Details</Form.Title>
        <Form.Table>
          {profiles.map((profile) => (
            <Form.Tr key={profile.id}>
              <Form.Tr>
                <Form.Th>Field</Form.Th>
                <Form.Th>Value</Form.Th>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Profile UID</Form.Td>
                <Form.Td>{profile.uid}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Profile Type</Form.Td>
                <Form.Td>{profile.profileType}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Profile Name</Form.Td>
                <Form.Td>{profile.profileName}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Email</Form.Td>
                <Form.Td>{userLocal.email}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Phone</Form.Td>
                <Form.Td>{profile.phone}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Description</Form.Td>
                <Form.Td>{profile.description}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Address</Form.Td>
                <Form.Td>{profile.address}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Profile Date</Form.Td>
                <Form.Td>{profile.profileDate}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Profile Link</Form.Td>
                <Form.DriveLink driveLink={profile.profileLink} drive={drive} />
              </Form.Tr>

              <Form.Text>
                <ReviewersView
                  reviewee={userLocal.uid}
                  docID={userLocal.uid}
                  docType="reviewer"
                />
              </Form.Text>

              <Form.Text>
                <Reviewer
                  reviewee={userLocal.uid}
                  docID={profile.id}
                  docType="reviewer"
                />
              </Form.Text>

              <ShowAdminMenu profileType={profile.profileType} />
            </Form.Tr>
          ))}
        </Form.Table>
        <Form.Link to="/profileview">View Profile</Form.Link>
        <Form.Link to="/profile">Update Profile</Form.Link>
        <Form.Link to="/browse">Return to Browse</Form.Link>
        <Form.TextSmall>You have an Awesome Profile!</Form.TextSmall>
      </Form>
    </>
  );
}
