import React, { useState, useContext } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//SK
import { db } from '../context/firebase';

export default function DemographicUpdate(props) {
  const { firebase } = useContext(FirebaseContext);

  const [error] = useState('');
  //SK
  var user = firebase.auth().currentUser;
  const [setLoader] = useState(false);

  const [demographicType, setDemographicType] = useState(
    props.docObject.demographicType
  );
  const [demographicName, setDemographicName] = useState(
    props.docObject.demographicName
  );

  const [birthday, setBirthday] = useState(props.docObject.birthday);
  const [birthplace, setBirthplace] = useState(props.docObject.birthplace);

  const [gender, setGender] = useState(props.docObject.gender);

  const [ethnicity, setEthnicity] = useState(props.docObject.ethnicity);
  const [hispanicLatino, setHispanicLatino] = useState(
    props.docObject.hispanicLatino
  );

  const [religion, setReligion] = useState(props.docObject.religion);
  const [military, setMilitary] = useState(props.docObject.military);

  const [citizenshipStatus, setCitizenshipStatus] = useState(
    props.docObject.citizenshipStatus
  );

  const [description, setDescription] = useState(props.docObject.description);
  const [startDate, setStartDate] = useState(props.docObject.startDate);

  const [demographicLink, setDemographicLink] = useState(
    props.docObject.demographicLink
  );
  const [updates] = useState(props.docObject.updates + 1);

  const [deleteDocument, setDeleteDocument] = useState(
    props.docObject.deleteDocument
  );

  const isInvalid = demographicName === '';

  const handleDemographicUpdate = (event) => {
    event.preventDefault();

    db.collection('demographics')
      .doc(props.docID)
      .set(
        {
          demographicType: demographicType,
          demographicName: demographicName,
          birthday: birthday,
          birthplace: birthplace,
          gender: gender,

          ethnicity: ethnicity,
          hispanicLatino: hispanicLatino,
          religion: religion,
          military: military,
          citizenshipStatus: citizenshipStatus,

          description: description,
          startDate: startDate,
          demographicLink: demographicLink,

          createdAt: new Date(),
          createdBy: user.uid,
          updates: updates,
          updatedAt: new Date(),
          updatedBy: user.uid,
          deleteDocument: deleteDocument,

          users: [
            {
              userId: user.uid,
            },
          ],
        },
        { merge: true }
      )
      .then(() => {
        setLoader(false);
        alert('Your demographics have been updated!');
      })
      .catch((error) => {
        alert(error.message);
        setLoader(false);
      });
  };

  return (
    <>
      <Form>
        <Form.SubTitle>Update Demographics</Form.SubTitle>
        {error && <Form.Error>{error}</Form.Error>}

        <Form.Base onSubmit={handleDemographicUpdate} method="POST">
          <label>
            <Form.Text>Choose Demographic Type:</Form.Text>
            <select
              value={demographicType}
              data-lpignore="true"
              onChange={({ target }) => setDemographicType(target.value)}
            >
              {' '}
              <option value="personal">Personal</option>
              <option value="family">Family</option>
              <option value="other">Other</option>
            </select>
          </label>

          <Form.Input
            placeholder="Demographic Type"
            value={demographicType}
            data-lpignore="true"
            onChange={({ target }) => setDemographicType(target.value)}
          />

          <Form.Input
            placeholder="Full Name"
            data-lpignore="true"
            value={demographicName}
            onChange={({ target }) => setDemographicName(target.value)}
          />

          <Form.Input
            placeholder="Birthday (MM/DD/YYYY)"
            value={birthday}
            data-lpignore="true"
            onChange={({ target }) => setBirthday(target.value)}
          />

          <Form.Input
            placeholder="Birthplace"
            value={birthplace}
            data-lpignore="true"
            onChange={({ target }) => setBirthplace(target.value)}
          />

          <Form.Input
            placeholder="Gender"
            value={gender}
            data-lpignore="true"
            onChange={({ target }) => setGender(target.value)}
          />

          <Form.Input
            placeholder="Ethnicity"
            value={ethnicity}
            data-lpignore="true"
            onChange={({ target }) => setEthnicity(target.value)}
          />

          <label>
            <Form.Text>Hispanc or Latino?</Form.Text>
            <select
              value={hispanicLatino}
              onChange={({ target }) => setHispanicLatino(target.value)}
            >
              {' '}
              <option value="Yes">Yes</option>
              <option value="No">No</option>
              <option value="None">None</option>
            </select>
          </label>

          <Form.Input
            placeholder="Religion"
            value={religion}
            data-lpignore="true"
            onChange={({ target }) => setReligion(target.value)}
          />

          <Form.Input
            placeholder="Military"
            value={military}
            data-lpignore="true"
            onChange={({ target }) => setMilitary(target.value)}
          />

          <Form.Input
            placeholder="Citizenship Status"
            value={citizenshipStatus}
            data-lpignore="true"
            onChange={({ target }) => setCitizenshipStatus(target.value)}
          />

          <Form.Input
            placeholder="Description"
            value={description}
            data-lpignore="true"
            onChange={({ target }) => setDescription(target.value)}
          />

          <Form.Input
            placeholder="Demographic Link"
            value={demographicLink}
            data-lpignore="true"
            onChange={({ target }) => setDemographicLink(target.value)}
          />

          <label>
            <Form.Text>Delete Demographic?</Form.Text>
            <Form.Input
              placeholder="Delete Demographic"
              type="checkbox"
              checked={deleteDocument}
              onChange={({ target }) => setDeleteDocument(target.value)}
            />
          </label>

          <Form.Submit
            disabled={isInvalid}
            type="submit"
            data-testid="update-demographic"
          >
            Save Demographic Update
          </Form.Submit>
        </Form.Base>

        <Form.Text>
          Want to see Demographic?{' '}
          <Form.Link to="/demographicview">View Demographics</Form.Link>
        </Form.Text>
        <Form.Link to="/menu-stories-view">Return to Stories</Form.Link>
        <Form.Link to="/browse">Return to Browse</Form.Link>

        <Form.TextSmall>You are so awesome!</Form.TextSmall>
      </Form>
    </>
  );
}
