import React, { useState, useEffect } from 'react';
import { Form } from '../components';

//import * as ROUTES from '../constants/routes';

//SK
import { db } from '../context/firebase';

//SK Must pass in props to get docID and DocType.
export default function CommentsView(props) {
  const [error, setError] = useState('');
  //SK
  const [comments, setComments] = useState([]);
  const [countComments, setCountComments] = useState(0);

  //SK Props
  //console.log('commentsview.js: props: docID:' + props.docID);
  //console.log('commentsview.js: props: docType:' + props.docType);

  // SK
  const [userLocal] = useState(JSON.parse(localStorage.getItem('authUser')));
  //const { user } = useAuthListener();

  // SK get the length from fetched Comments.
  //setCountComments(fetchedComments.length);

  useEffect(() => {
    db.collection('comment')
      .where('reviewee', '==', userLocal.uid)
      .where('docID', '==', props.docID)
      .where('commentType', '==', props.docType)
      .get()
      .then((response) => {
        const fetchedComments = [];
        response.docs.forEach((document) => {
          const fetchedComment = {
            id: document.id,
            ...document.data(),
          };
          fetchedComments.push(fetchedComment);
        });
        setComments(fetchedComments);
        setCountComments(fetchedComments.length);
      })
      .catch((error) => {
        setError(error);
      });
  }, []);

  return countComments > 0 ? (
    <>
      <Form.SubTitle>View Comments</Form.SubTitle>
      {error && <Form.Error>{error}</Form.Error>}

      <Form.Text>
        {error ? <p>Ops, there is an error :(</p> : null}
        {comments.map((comment) => (
          <Form.Text key={comment.id}>
            {comment.commentName} <br></br> {comment.description}
          </Form.Text>
        ))}
      </Form.Text>
    </>
  ) : null;
}
