import React, { useState, useContext, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';
import CommentsView from './commentsview';

//SK
import { db } from '../context/firebase';

//SK Google Drive
import drive from '../social/drive.png';

import Grid from '../components/grid';
import { SidebarStories } from '../components/layout/sidebar-stories';
import { UniHeader } from '../containers/universilyheader';
import { FooterContainer } from '../containers/footer';

export default function EssayView() {
  return (
    <Grid>
      <Grid.Header>
        <UniHeader />
      </Grid.Header>
      <Grid.Main>
        <EssayViewPage />
      </Grid.Main>
      <Grid.Aside>
        <SidebarStories />
      </Grid.Aside>
      <Grid.Footer>
        <FooterContainer />
      </Grid.Footer>
    </Grid>
  );
}
export function EssayViewPage() {
  const history = useHistory();
  const { firebase } = useContext(FirebaseContext);

  const [error, setError] = useState('');
  //SK
  //var user = firebase.auth().currentUser;
  const [essays, setEssays] = useState([]);
  // SK
  const [userLocal, setUserLocal] = useState(
    JSON.parse(localStorage.getItem('authUser'))
  );

  firebase.auth().onAuthStateChanged(function (user) {
    if (user) {
      // User is signed in.
      var uid = user.uid;
      console.log('user is signed in for onAuthStateChanged: uid:' + uid);
    } else {
      // No user is signed in.
      console.log('user is NOT signed in for onAuthStateChanged.');
      var uid = '';
    }
  });

  //       .where('createdBy', '==', user.uid)
  //       .where('createdBy', '==', uid)
  //      .where('createdBy', '==', userLocal.uid)

  useEffect(() => {
    db.collection('essay')
      .where('createdBy', '==', userLocal.uid)
      .get()
      .then((response) => {
        const fetchedEssays = [];
        response.docs.forEach((document) => {
          const fetchedEssay = {
            id: document.id,
            ...document.data(),
          };
          fetchedEssays.push(fetchedEssay);
        });
        setEssays(fetchedEssays);
      })
      .catch((error) => {
        setError(error);
      });
  }, []);

  return (
    <>
      <Form>
        <Form.SubTitle>View Essays</Form.SubTitle>
        {error && <Form.Error>{error}</Form.Error>}

        <Form.Text>
          <Form.Table>
            <Form.Thead>
              <Form.Tr>
                <Form.Th>Type</Form.Th>
                <Form.Th>Name</Form.Th>
                <Form.Th>Description</Form.Th>
                <Form.Th>Length</Form.Th>

                <Form.Th>Date</Form.Th>

                <Form.Th>Status</Form.Th>
                <Form.Th>Review</Form.Th>
              </Form.Tr>
            </Form.Thead>

            <Form.Tbody>
              {error ? <p>Ops, there is an error :(</p> : null}
              {essays.map((essay) => (
                <Form.Tr key={essay.id}>
                  <Form.Td> {essay.essayType}</Form.Td>

                  <Form.Td> {essay.essayName}</Form.Td>

                  <Form.Td> {essay.description}</Form.Td>
                  <Form.Td> {essay.length}</Form.Td>

                  <Form.Td> {essay.essayDate}</Form.Td>

                  <Form.Td> {essay.status}</Form.Td>
                  <Form.Td> {essay.review}</Form.Td>
                </Form.Tr>
              ))}
            </Form.Tbody>
          </Form.Table>
          Want to add more Essays?
          <Form.Link to="/essay">Add Essay</Form.Link>
          <Form.SubTitle>Essay Details</Form.SubTitle>
          <Form.Table>
            {essays.map((essay) => (
              <Form.Tr key={essay.id}>
                <Form.Tr>
                  <Form.Th>Field</Form.Th>
                  <Form.Th>Value</Form.Th>
                </Form.Tr>

                <Form.Tr>
                  <Form.Td>Essay Type</Form.Td>
                  <Form.Td>{essay.essayType}</Form.Td>
                </Form.Tr>

                <Form.Tr>
                  <Form.Td>Essay Name</Form.Td>
                  <Form.Td>{essay.essayName}</Form.Td>
                </Form.Tr>

                <Form.Tr>
                  <Form.Td>Description</Form.Td>
                  <Form.Td>{essay.description}</Form.Td>
                </Form.Tr>

                <Form.Tr>
                  <Form.Td>Length</Form.Td>
                  <Form.Td>{essay.length}</Form.Td>
                </Form.Tr>

                <Form.Tr>
                  <Form.Td>Essay Date</Form.Td>
                  <Form.Td>{essay.essayDate}</Form.Td>
                </Form.Tr>

                <Form.Tr>
                  <Form.Td>Status</Form.Td>
                  <Form.Td> {essay.status}</Form.Td>
                </Form.Tr>

                <Form.Tr>
                  <Form.Td>Review</Form.Td>
                  <Form.Td> {essay.review}</Form.Td>
                </Form.Tr>

                <Form.Tr>
                  <Form.Td>Drive Link</Form.Td>
                  <Form.DriveLink driveLink={essay.essayLink} drive={drive} />
                </Form.Tr>

                <Form.Text>
                  <CommentsView
                    reviewee={userLocal.uid}
                    docID={essay.id}
                    docType="essay"
                  />
                </Form.Text>
              </Form.Tr>
            ))}
          </Form.Table>
        </Form.Text>
        <Form.Link to="/menu-stories-view">Return to Stories</Form.Link>
        <Form.Link to="/browse">Return to Browse</Form.Link>

        <Form.TextSmall>You are so awesome!</Form.TextSmall>
      </Form>
    </>
  );
}
