import React, { useState, useContext } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//import { ShowDelete } from '../containers/showdelete';

//SK
import { db } from '../context/firebase';

export default function NicheUpdate(props) {
  const { firebase } = useContext(FirebaseContext);

  const [error] = useState('');
  //SK
  var user = firebase.auth().currentUser;
  const [setLoader] = useState(false);

  const [nicheType, setNicheType] = useState(props.docObject.nicheType);
  const [nicheName, setNicheName] = useState(props.docObject.nicheName);
  const [description, setDescription] = useState(props.docObject.description);

  const [usage] = useState(props.docObject.usage);
  const [updates] = useState(props.docObject.updates + 1);

  const [nicheDate, setNicheDate] = useState(props.docObject.nicheDate);
  const [nicheLink, setNicheLink] = useState(props.docObject.nicheLink);

  const [deleteDocument, setDeleteDocument] = useState(
    props.docObject.deleteDocument
  );

  const ShowDelete = () => {
    const [showText, setShowText] = useState(false);

    return (
      <div>
        {!deleteDocument && (
          <button
            onClick={() => {
              setShowText(!showText);
              console.log('showText: ' + showText);
            }}
          >
            Delete
          </button>
        )}

        {showText && (
          <label>
            <Form.Text>Confirm Delete Niche</Form.Text>
            <Form.Input
              placeholder="Confirm Delete Niche"
              type="checkbox"
              checked={deleteDocument}
              onChange={({ target }) => setDeleteDocument(target.value)}
            />
          </label>
        )}
        {deleteDocument && (
          <Form.Text>
            Please press [Save Niche] button to complete the Delete!
          </Form.Text>
        )}
      </div>
    );
  };

  const isInvalid = nicheName === '';

  const handleNicheUpdate = (event) => {
    event.preventDefault();

    db.collection('niche')
      .doc(props.docID)
      .set(
        {
          nicheType: nicheType,
          nicheName: nicheName,
          description: description,
          usage: usage,
          updates: updates,
          nicheDate: nicheDate,
          nicheLink: nicheLink,
          updatedAt: new Date(),
          updatedBy: user.uid,
          deleteDocument: deleteDocument,

          users: [
            {
              userId: user.uid,
            },
          ],
        },
        { merge: true }
      )
      .then(() => {
        setLoader(false);
        //alert('Your niche has been updated!');
        console.log(
          'Niche Updated: ' + props.docID + 'deleteDocument:' + deleteDocument
        );
      })
      .catch((error) => {
        alert(error.message);
        setLoader(false);
      });
  };

  return (
    <>
      <Form>
        <Form.SubTitle>Update Niche</Form.SubTitle>
        {error && <Form.Error>{error}</Form.Error>}

        <Form.Base onSubmit={handleNicheUpdate} method="POST">
          <Form.Input
            placeholder="Niche Type"
            data-lpignore="true"
            value={nicheType}
            onChange={({ target }) => setNicheType(target.value)}
          />

          <Form.Input
            placeholder="Niche Name"
            data-lpignore="true"
            value={nicheName}
            onChange={({ target }) => setNicheName(target.value)}
          />

          <Form.Input
            placeholder="Description"
            data-lpignore="true"
            value={description}
            onChange={({ target }) => setDescription(target.value)}
          />
          <Form.Input
            placeholder="Niche Date"
            type="date"
            value={nicheDate}
            onChange={({ target }) => setNicheDate(target.value)}
          />
          <Form.Input
            placeholder="Niche Link"
            data-lpignore="true"
            value={nicheLink}
            onChange={({ target }) => setNicheLink(target.value)}
          />

          <ShowDelete />

          <Form.Submit
            disabled={isInvalid}
            type="submit"
            data-testid="niche-update"
          >
            Save Niche
          </Form.Submit>
        </Form.Base>

        <Form.Text>
          Want to see Niches? <Form.Link to="/nicheview">View Niches</Form.Link>
        </Form.Text>
        <Form.Link to="/menu-stories-view">Return to Stories</Form.Link>
        <Form.Link to="/browse">Return to Browse</Form.Link>
        <Form.TextSmall>You are so awesome!</Form.TextSmall>
      </Form>
    </>
  );
}
