import React, { useState, useContext, useEffect } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//SK
import { db } from '../context/firebase';

export default function ProfileViewSummary() {
  const { firebase } = useContext(FirebaseContext);

  const [error, setError] = useState('');

  const [profiles, setProfiles] = useState([]);
  // SK
  const [userLocal, setUserLocal] = useState(
    JSON.parse(localStorage.getItem('authUser'))
  );

  useEffect(() => {
    db.collection('profile')
      .where('uid', '==', userLocal.uid)
      .where('deleteDocument', '==', false)
      .get()
      .then((response) => {
        const fetchedProfiles = [];
        response.docs.forEach((document) => {
          const fetchedProfile = {
            id: document.id,
            ...document.data(),
          };
          fetchedProfiles.push(fetchedProfile);
        });
        setProfiles(fetchedProfiles);
      })
      .catch((error) => {
        setError(error);
      });
  }, []);

  return (
    <>
      <Form.SubTitle>View Profile</Form.SubTitle>
      {error && <Form.Error>{error}</Form.Error>}
      <Form.Table>
        <Form.Thead>
          <Form.Tr>
            <Form.Th>Type</Form.Th>
            <Form.Th>Name</Form.Th>
            <Form.Th>Email</Form.Th>
            <Form.Th>Phone</Form.Th>

            <Form.Th>Description</Form.Th>
            <Form.Th>Usage</Form.Th>

            <Form.Th>Date</Form.Th>
          </Form.Tr>
        </Form.Thead>
        <Form.Tbody>
          {error ? <p>Ops, there is an error :(</p> : null}
          {profiles.map((profile) => (
            <Form.Tr key={profile.id}>
              <Form.Td> {profile.profileType}</Form.Td>
              <Form.Td> {profile.profileName}</Form.Td>
              <Form.Td> {userLocal.email}</Form.Td>
              <Form.Td> {profile.phone}</Form.Td>

              <Form.Td> {profile.description}</Form.Td>
              <Form.Td> {profile.address}</Form.Td>

              <Form.Td> {profile.profileDate}</Form.Td>
            </Form.Tr>
          ))}
        </Form.Tbody>
      </Form.Table>
    </>
  );
}
