function Search(props) {
  const [showResults, setShowResults] = React.useState(false);
  const onClick = () => setShowResults(true);

  //SK Set the docID passed from the link.
  //setDocumentID({ props.docID });
  console.log('ParentView:props:' + JSON.stringify(props));
  console.log('ParentView:props: docID:' + props.docID);

  return (
    <div>
      <input type="submit" value="Add Reviewer" onClick={onClick} />
      {showResults ? (
        <Form.Text>
          <Reviewer
            reviewee={props.reviewee}
            docID={props.DocID}
            docType="reviewer"
          />
        </Form.Text>
      ) : null}
    </div>
  );
}

