export function ParentViewPage() {
  const { firebase } = useContext(FirebaseContext);

  const [error, setError] = useState('');

  //SK Trying to refresh list of reviewers after adding a new one.
  //const [refresh, setRefresh] = useState(0);
  //console.log('refresh:' + refresh);

  //SK
  //var user = firebase.auth().currentUser;
  const [parents, setParents] = useState([]);
  // SK
  const [userLocal, setUserLocal] = useState(
    JSON.parse(localStorage.getItem('authUser'))
  );

  firebase.auth().onAuthStateChanged(function (user) {
    if (user) {
      // User is signed in.
      var uid = user.uid;
      console.log('user is signed in for onAuthStateChanged: uid:' + uid);
    } else {
      // No user is signed in.
      console.log('user is NOT signed in for onAuthStateChanged.');
      var uid = '';
    }
  });

  //       .where('createdBy', '==', user.uid)
  //       .where('createdBy', '==', uid)
  //      .where('createdBy', '==', userLocal.uid)

  useEffect(() => {
    db.collection('parent')
      .where('uid', '==', userLocal.uid)
      .get()
      .then((response) => {
        const fetchedParents = [];
        response.docs.forEach((document) => {
          const fetchedParent = {
            id: document.id,
            ...document.data(),
          };
          fetchedParents.push(fetchedParent);
        });
        setParents(fetchedParents);
      })
      .catch((error) => {
        setError(error);
      });
  }, []);

  return (
    <>
      <Form>
        <Form.Title>View Parent</Form.Title>
        {error && <Form.Error>{error}</Form.Error>}
        <Form.Table>
          <Form.Thead>
            <Form.Tr>
              <Form.Th>Type</Form.Th>
              <Form.Th>Name</Form.Th>
              <Form.Th>Birthplace</Form.Th>

              <Form.Th>Email</Form.Th>
              <Form.Th>Phone</Form.Th>

              <Form.Th>Occupation</Form.Th>
              <Form.Th>Education</Form.Th>

              <Form.Th>Description</Form.Th>
              <Form.Th>Usage</Form.Th>

              <Form.Th>Date</Form.Th>
            </Form.Tr>
          </Form.Thead>
          <Form.Tbody>
            {error ? <p>Ops, there is an error :(</p> : null}
            {parents.map((parent) => (
              <Form.Tr key={parent.id}>
                <Form.Td> {parent.parentType}</Form.Td>
                <Form.Td> {parent.parentName}</Form.Td>
                <Form.Td> {parent.parentBirthplace}</Form.Td>

                <Form.Td> {parent.email}</Form.Td>
                <Form.Td> {parent.phone}</Form.Td>

                <Form.Td> {parent.occupation}</Form.Td>
                <Form.Td> {parent.education}</Form.Td>

                <Form.Td> {parent.description}</Form.Td>
                <Form.Td> {parent.address}</Form.Td>

                <Form.Td> {parent.parentDate}</Form.Td>
              </Form.Tr>
            ))}
          </Form.Tbody>
        </Form.Table>
        <Form.Link to="/ParentView">View parent</Form.Link>
        <Form.Link to="/parent">Add Parent</Form.Link>
        <Form.Title>Parent Details</Form.Title>
        <Form.Table>
          {parents.map((parent) => (
            <Form.Tr key={parent.id}>
              <Form.Tr>
                <Form.Th>Field</Form.Th>
                <Form.Th>Value</Form.Th>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Parent UID</Form.Td>
                <Form.Td>{parent.uid}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Parent Type</Form.Td>
                <Form.Td>{parent.parentType}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Parent Name</Form.Td>
                <Form.Td>{parent.parentName}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Parent Birthplace</Form.Td>
                <Form.Td>{parent.parentBirthplace}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Email</Form.Td>
                <Form.Td>{parent.email}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Phone</Form.Td>
                <Form.Td>{parent.phone}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Occupation</Form.Td>
                <Form.Td>{parent.occupation}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Education</Form.Td>
                <Form.Td>{parent.education}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Description</Form.Td>
                <Form.Td>{parent.description}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Address</Form.Td>
                <Form.Td>{parent.address}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Parent Date</Form.Td>
                <Form.Td>{parent.parentDate}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Parent Link</Form.Td>
                <Form.DriveLink driveLink={parent.parentLink} drive={drive} />
              </Form.Tr>
              <Form.Tr>
                <Form.Td>.</Form.Td>
              </Form.Tr>
            </Form.Tr>
          ))}
        </Form.Table>
        <Form.Link to="/ParentView">View Parent</Form.Link>
        <Form.Link to="/parent">Update Parent</Form.Link>
        <Form.Link to="/menuview">Return to Menu</Form.Link>

        <Form.Link to="/browse">Return to Browse</Form.Link>
        <Form.TextSmall>You have an Awesome Family!</Form.TextSmall>
      </Form>
    </>
  );
}
