import React, { useState, useContext } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//SK
import { db } from '../context/firebase';

export default function Course() {
  const { firebase } = useContext(FirebaseContext);

  const [error] = useState('');
  //SK
  var user = firebase.auth().currentUser;
  const [setLoader] = useState(false);

  const [courseType, setCourseType] = useState('');
  const [courseName, setCourseName] = useState('');
  const [courseGrade, setCourseGrade] = useState('');
  const [courseSemester, setCourseSemester] = useState('');

  const [description, setDescription] = useState('');
  const [usage, setUsage] = useState(0);
  const [updates, setUpdates] = useState(0);

  const [courseDate, setCourseDate] = useState('');
  const [courseLink, setCourseLink] = useState('');

  const [deleteDocument, setDeleteDocument] = useState(false);


  const isInvalid = courseName === '';

  const handleCourse = (event) => {
    event.preventDefault();

    db.collection('course')
      .add({
        courseType: courseType,
        courseName: courseName,
        courseGrade: courseGrade,
        courseSemester: courseSemester,
        description: description,
        usage: usage,
        updates: updates,
        courseDate: courseDate,
        courseLink: courseLink,
        createdAt: new Date(),
        createdBy: user.uid,
        deleteDocument: deleteDocument,
        users: [
          {
            userId: user.uid,
          },
        ],
      })
      .then(() => {
        setLoader(false);
        alert('Your course has been saved!');
      })
      .catch((error) => {
        alert(error.message);
        setLoader(false);
      });
    setCourseType('');
    setCourseName('');
    setCourseGrade('');
    setCourseSemester('');

    setDescription('');
    setUsage(0);
    setUpdates(0);

    setCourseDate('');
    setCourseLink('');

    setDeleteDocument(false);

  };

  return (
    <>
      <Form>
        <Form.Title>Add Course</Form.Title>
        {error && <Form.Error>{error}</Form.Error>}

        <Form.Base onSubmit={handleCourse} method="POST">
          <Form.Input
            placeholder="Course Type"
            data-lpignore="true"
            value={courseType}
            onChange={({ target }) => setCourseType(target.value)}
          />

          <Form.Input
            placeholder="Course Name"
            data-lpignore="true"
            value={courseName}
            onChange={({ target }) => setCourseName(target.value)}
          />
          <Form.Input
            placeholder="Course Grade"
            data-lpignore="true"
            value={courseGrade}
            onChange={({ target }) => setCourseGrade(target.value)}
          />
          <Form.Input
            placeholder="Course Semester"
            data-lpignore="true"
            value={courseSemester}
            onChange={({ target }) => setCourseSemester(target.value)}
          />

          <Form.Input
            placeholder="Description"
            data-lpignore="true"
            value={description}
            onChange={({ target }) => setDescription(target.value)}
          />
          <Form.Input
            placeholder="Course Date"
            type="date"
            value={courseDate}
            onChange={({ target }) => setCourseDate(target.value)}
          />
          <Form.Input
            placeholder="Course Link"
            data-lpignore="true"
            value={courseLink}
            onChange={({ target }) => setCourseLink(target.value)}
          />
          <Form.Submit disabled={isInvalid} type="submit" data-testid="sign-up">
            Save Course
          </Form.Submit>
        </Form.Base>

        <Form.Text>
          Want to see Courses?{' '}
          <Form.Link to="/courseview">View Courses</Form.Link>
        </Form.Text>
        <Form.Link to="/menu-stories-view">Return to Stories</Form.Link>
        <Form.Link to="/browse">Return to Browse</Form.Link>
        <Form.TextSmall>You are so awesome!</Form.TextSmall>
      </Form>
    </>
  );
}
