import React, { useState, useContext, useEffect } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//SK
import { db } from '../context/firebase';

export default function ApplicationViewSummary() {

  const [error, setError] = useState('');

  const [applications, setApplications] = useState([]);
  // SK
  const [userLocal, setUserLocal] = useState(
    JSON.parse(localStorage.getItem('authUser'))
  );

  //       .where('uid', '==', userLocal.uid)


  useEffect(() => {
    db.collection('applications')
      .get()
      .then((response) => {
        const fetchedApplications = [];
        response.docs.forEach((document) => {
          const fetchedApplication = {
            id: document.id,
            ...document.data(),
          };
          fetchedApplications.push(fetchedApplication);

        });
        setApplications(fetchedApplications);
       //     console.log('fetchedApplications:' + JSON.stringify(fetchedApplications));
       //     console.log('setApplications:' + JSON.stringify(applications));


      })
      .catch((error) => {
        setError(error);
      });
  }, []);

  return (
    <>
      <Form.Title>View Applications</Form.Title>
      {error && <Form.Error>{error}</Form.Error>}
      <Form.Table>
        <Form.Thead>
          <Form.Tr>
            <Form.Th>Title</Form.Th>
            <Form.Th>Description</Form.Th>
           
          </Form.Tr>
        </Form.Thead>
        <Form.Tbody>
          {error ? <p>Ops, there is an error :(</p> : null}
          {applications.map((application) => (
            <Form.Tr key={application.id}>
              <Form.Td> {application.title}</Form.Td>
              <Form.Td> {application.description}</Form.Td>
            </Form.Tr>
          ))}
        </Form.Tbody>
      </Form.Table>
    </>
  );
}
