import React, { useState, useContext } from 'react';
import { makeStyles } from '@material-ui/core/styles';
// import { mediaQueries } from "../../../utils/mediaQuery"
import InputLabel from '@material-ui/core/InputLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import NativeSelect from '@material-ui/core/NativeSelect';
import TextField from '@material-ui/core/TextField';
import { Link, useHistory } from 'react-router-dom';
import Button from '@material-ui/core/Button';
import { green } from '@material-ui/core/colors';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
// import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
// import Favorite from '@material-ui/icons/Favorite';
// import FavoriteBorder from '@material-ui/icons/FavoriteBorder';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';
import { HeaderContainer } from '../containers/header';

// import { FooterContainer } from '../containers/footer';
import * as ROUTES from '../constants/routes';
import {
  LoginMain,
  LoginForm,
  LogoBlock,
  LoginTitleBlock,
  CustomForm,
  ForgotLink,
  BtnCommon,
  ErrorMessage,
  LastLink,
} from '../components/signin/styles/signin';

export default function SignIn() {
  const useStyles = makeStyles((theme) => ({
    formControl: {
      margin: theme.spacing(1),
      minWidth: 120,
    },
    selectEmpty: {
      marginTop: theme.spacing(2),
    },
    root: {
      '& .MuiTextField-root': {
        margin: theme.spacing(1),
        width: '25ch',
      },
    },
  }));

  const classes = useStyles();
  const history = useHistory();

  const { firebase } = useContext(FirebaseContext);

  const [emailAddress, setEmailAddress] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [age, setAge] = useState('');

  const isInvalid = password === '' || emailAddress === '';

  const handleSignin = (event) => {
    event.preventDefault();

    return firebase
      .auth()
      .signInWithEmailAndPassword(emailAddress, password)
      .then(() => {
        history.push(ROUTES.BROWSE);
      })
      .catch((error) => {
        setEmailAddress('');
        setPassword('');
        setError(error.message);
      });
  };

  const handleChange = (event) => {
    const { name } = event.target;
    setAge(name);
  };

  return (
    <>
      <LoginMain>
        <LoginForm>
          <LogoBlock>
            <a href="#">
              {/* <img src="../images/logo.svg></img> */}
              <img src="/images/logo.svg" alt="logo" />
            </a>
          </LogoBlock>
          <LoginTitleBlock>
            <h2>Login</h2>
            <FormControl className={classes.formControl}>
              <NativeSelect
                // value={age}
                onChange={handleChange}
                name="age"
                className={classes.selectEmpty}
                inputProps={{ 'aria-label': 'age' }}
              >
                <option value="">English</option>
                <option value={10}>English</option>
                <option value={20}>English</option>
                <option value={30}>English</option>
              </NativeSelect>
            </FormControl>
          </LoginTitleBlock>
          <CustomForm onSubmit={handleSignin} method="POST">
            <TextField
              id="outlined-helperText"
              label="Email"
              // defaultValue="Email"
              type="email"
              value={emailAddress}
              onChange={({ target }) => setEmailAddress(target.value)}
              // helperText="Some important text"
              variant="outlined"
            />
            <TextField
              id="outlined-helperText"
              label="Password"
              type="password"
              value={password}
              onChange={({ target }) => setPassword(target.value)}
              // helperText="Some important text"
              variant="outlined"
            />
            <ForgotLink>
              {/* <a href="#">Forgot Password?</a> */}
              <Link to={ROUTES.FORGOT_PASSWORD}>Forgot Password?</Link>
            </ForgotLink>
            <ErrorMessage>{error}</ErrorMessage>
            <BtnCommon>
              <Button variant="contained" color="primary" type="submit" disabled={isInvalid}>
                LOGIN
              </Button>
            </BtnCommon>
          </CustomForm>
          <LastLink>
            <p>
              New to Universily? <Link to={ROUTES.SIGN_UP}>Sign up now</Link>.
            </p>
          </LastLink>
        </LoginForm>
      </LoginMain>
      {/* <HeaderContainer> */}

      {/* <Form>
          <Form.Title>Sign In</Form.Title>
          {error && <Form.Error data-testid="error">{error}</Form.Error>}

          <Form.Base onSubmit={handleSignin} method="POST">
            <Form.Input
              placeholder="Email address"
              value={emailAddress}
              onChange={({ target }) => setEmailAddress(target.value)}
            />
            <Form.Input
              type="password"
              value={password}
              autoComplete="off"
              placeholder="Password"
              onChange={({ target }) => setPassword(target.value)}
            />
            <Form.Submit
              disabled={isInvalid}
              type="submit"
              data-testid="sign-in"
            >
              Sign In
            </Form.Submit>
          </Form.Base>

          <Form.Text>
            New to Universily? <Form.Link to="/signup">Sign up now.</Form.Link>
          </Form.Text>
          <Form.TextSmall>
            This page is protected by Google reCAPTCHA to ensure you're not a
            bot. Learn more.
          </Form.TextSmall>
        </Form> */}
      {/* </HeaderContainer> */}
      {/* <FooterContainer /> */}
    </>
  );
}
