import React, { useState, useContext, useEffect } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';
import CommentsView from './commentsview';
import HonorUpdate from './honorupdate';
import { Content } from '../components/layout/content';

//SK
import { db } from '../context/firebase';

//SK Google Drive
import drive from '../social/drive.png';

import Grid from '../components/grid';
import { SidebarStories } from '../components/layout/sidebar-stories';
import { UniHeader } from '../containers/universilyheader';
import { FooterContainer } from '../containers/footer';

export default function HonorView() {
  return (
    <Grid>
      <Grid.Header>
        <UniHeader />
      </Grid.Header>
      <Grid.Main>
        <HonorViewPage />
      </Grid.Main>
      <Grid.Aside>
        <SidebarStories />
      </Grid.Aside>
      <Grid.Footer>
        <FooterContainer />
      </Grid.Footer>
    </Grid>
  );
}

export function HonorViewPage() {
  // const { firebase } = useContext(FirebaseContext);

  const [error, setError] = useState('');
  //SK
  const [honors, sethonors] = useState([]);
  // SK
  const [userLocal] = useState(JSON.parse(localStorage.getItem('authUser')));

  useEffect(() => {
    db.collection('honor')
      .where('createdBy', '==', userLocal.uid)
      .where('deleteDocument', '==', false)
      .get()
      .then((response) => {
        const fetchedhonors = [];
        response.docs.forEach((document) => {
          const fetchedhonor = {
            id: document.id,
            ...document.data(),
          };
          fetchedhonors.push(fetchedhonor);
        });
        sethonors(fetchedhonors);
      })
      .catch((error) => {
        setError(error);
      });
  }, [userLocal.uid]);

  return (
    <>
      <Form>
        <Form.SubTitle>View Honors</Form.SubTitle>
        {error && <Form.Error>{error}</Form.Error>}
        <Form.Table>
          <Form.Thead>
            <Form.Tr>
              <Form.Th>Type</Form.Th>
              <Form.Th>Name</Form.Th>
              <Form.Th>Scope</Form.Th>

              <Form.Th>Description</Form.Th>
              <Form.Th>Usage</Form.Th>
              <Form.Th>Updates</Form.Th>

              <Form.Th>Date</Form.Th>

              <Form.Th>Years</Form.Th>
              <Form.Th>Niche</Form.Th>
              <Form.Th>Review</Form.Th>
            </Form.Tr>
          </Form.Thead>
          <Form.Tbody>
            {error ? <p>Ops, there is an error :(</p> : null}
            {honors.map((honor) => (
              <Form.Tr key={honor.id}>
                <Form.Td> {honor.honorType}</Form.Td>
                <Form.Td> {honor.honorName}</Form.Td>
                <Form.Td> {honor.honorScope}</Form.Td>

                <Form.Td> {honor.description}</Form.Td>
                <Form.Td> {honor.usage}</Form.Td>
                <Form.Td> {honor.updates}</Form.Td>

                <Form.Td> {honor.honorDate}</Form.Td>

                <Form.Td> {honor.years}</Form.Td>
                <Form.Td> {honor.niche}</Form.Td>

                <Form.Td> {honor.review}</Form.Td>
              </Form.Tr>
            ))}
          </Form.Tbody>
        </Form.Table>
        Want to add more Honors?
        <Form.Link to="/honor">Add Honor</Form.Link>
        <Form.SubTitle>Honor Details</Form.SubTitle>
        <Form.Table>
          {honors.map((honor) => (
            <Form.Tr key={honor.id}>
              <Form.Tr>
                <Form.Th>Field</Form.Th>
                <Form.Th>Value</Form.Th>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>honor Type</Form.Td>
                <Form.Td>{honor.honorType}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>honor Name</Form.Td>
                <Form.Td>{honor.honorName}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>honor Scope</Form.Td>
                <Form.Td>{honor.honorScope}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Description</Form.Td>
                <Form.Td>{honor.description}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Usage</Form.Td>
                <Form.Td>{honor.usage}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Updates</Form.Td>
                <Form.Td>{honor.updates}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>honor Date</Form.Td>
                <Form.Td>{honor.honorDate}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Years</Form.Td>
                <Form.Td>{honor.years}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Niche</Form.Td>
                <Form.Td>{honor.niche}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Review</Form.Td>
                <Form.Td>{honor.review}</Form.Td>
              </Form.Tr>

              <Form.Tr>
                <Form.Td>Drive Link</Form.Td>
                <Form.DriveLink driveLink={honor.honorLink} drive={drive} />
              </Form.Tr>

              <Form.Text>
                <CommentsView
                  reviewee={userLocal.uid}
                  docID={honor.id}
                  docType="honor"
                />
              </Form.Text>

              <Form.Text>
                <HonorUpdate
                  createdBy={userLocal.uid}
                  docID={honor.id}
                  docObject={honor}
                />
              </Form.Text>
            </Form.Tr>
          ))}
        </Form.Table>
        Want to add another honor?
        <Form.Link to="/honor">Add Honor</Form.Link>
        <Form.Link to="/menu-stories-view">Return to Stories</Form.Link>
        <Form.Link to="/browse">Return to Browse</Form.Link>
        <Form.TextSmall>You are in the Awesome honor!</Form.TextSmall>
      </Form>
    </>
  );
}
