import React, { useState, useContext, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';
import CommentsView from './commentsview';
import GuidanceView from './guidanceview';

import ActivityUpdate from './activityupdate';

//SK
import { db } from '../context/firebase';

//SK Google Drive
import drive from '../social/drive.png';

import Grid from '../components/grid';
import { SidebarStories } from '../components/layout/sidebar-stories';
import { UniHeader } from '../containers/universilyheader';
import { FooterContainer } from '../containers/footer';

export default function ActivityView() {
  return (
    <Grid>
      <Grid.Header>
        <UniHeader />
      </Grid.Header>
      <Grid.Main>
        <ActivityViewPage />
      </Grid.Main>
      <Grid.Aside>
        <SidebarStories />
      </Grid.Aside>
      <Grid.Footer>
        <FooterContainer />
      </Grid.Footer>
    </Grid>
  );
}

export function ActivityViewPage() {
  const history = useHistory();
  const { firebase } = useContext(FirebaseContext);

  const [error, setError] = useState('');
  //SK
  //var user = firebase.auth().currentUser;
  const [activities, setActivities] = useState([]);
  // SK
  const [userLocal, setUserLocal] = useState(
    JSON.parse(localStorage.getItem('authUser'))
  );
  //const { user } = useAuthListener();

  firebase.auth().onAuthStateChanged(function (user) {
    if (user) {
      // User is signed in.
      var uid = user.uid;
      console.log('user is signed in for onAuthStateChanged: uid:' + uid);
    } else {
      // No user is signed in.
      console.log('user is NOT signed in for onAuthStateChanged.');
      var uid = '';
    }
  });

  useEffect(() => {
    db.collection('activity')
      .where('createdBy', '==', userLocal.uid)
      .where('deleteDocument', '==', false)
      .get()
      .then((response) => {
        const fetchedActivities = [];
        response.docs.forEach((document) => {
          const fetchedActivity = {
            id: document.id,
            ...document.data(),
          };
          fetchedActivities.push(fetchedActivity);
        });
        setActivities(fetchedActivities);
      })
      .catch((error) => {
        setError(error);
      });
  }, []);

  return (
    <>
      <Form>
        <Form.SubTitle>View Activities</Form.SubTitle>
        {error && <Form.Error>{error}</Form.Error>}
        <Form.Text>
          <Form.Table>
            <Form.Thead>
              <Form.Tr>
                <Form.Th>Name</Form.Th>
                <Form.Th>Description</Form.Th>
                <Form.Th>Start Date</Form.Th>
                <Form.Th>Scope</Form.Th>
                <Form.Th>Activity</Form.Th>
                <Form.Th>Honor</Form.Th>
                <Form.Th>Additional Info</Form.Th>
                <Form.Th>Updates</Form.Th>
                <Form.Th>Status</Form.Th>
                <Form.Th>Review</Form.Th>
              </Form.Tr>
            </Form.Thead>

            {error ? <p>Ops, there is an error :(</p> : null}
            {activities.map((activity) => (
              <Form.Tr key={activity.id}>
                <Form.Td> {activity.activityName}</Form.Td>
                <Form.Td> {activity.description}</Form.Td>
                <Form.Td> {activity.startDate}</Form.Td>
                <Form.Td> {activity.activityScope}</Form.Td>
                <Form.Td> {activity.isActivity}</Form.Td>
                <Form.Td> {activity.isHonor}</Form.Td>
                <Form.Td> {activity.isAdditional}</Form.Td>
                <Form.Td> {activity.updates}</Form.Td>
                <Form.Td> {activity.status}</Form.Td>
                <Form.Td> {activity.review}</Form.Td>
              </Form.Tr>
            ))}
          </Form.Table>
        </Form.Text>
        <Form.Text>Want to add more Activites?</Form.Text>
        <Form.Link to="/activity">Add Activities</Form.Link>

        <Form.Text>
          <GuidanceView guidanceGenre="Activities" />
        </Form.Text>

        <Form.SubTitle>Activity Details</Form.SubTitle>

        <Form.Table>
          <Form.Tbody>
            {activities.map((activity) => (
              <Form.Tr key={activity.id}>
                <Form.Tr>
                  <Form.Th>Field</Form.Th>
                  <Form.Th>Value</Form.Th>
                </Form.Tr>

                <Form.Tr>
                  <Form.Td>Activity Type</Form.Td>
                  <Form.Td>{activity.activityType}</Form.Td>
                </Form.Tr>

                <Form.Tr>
                  <Form.Td>Activity Name</Form.Td>
                  <Form.Td>{activity.activityName}</Form.Td>
                </Form.Tr>

                <Form.Tr>
                  <Form.Td>Description</Form.Td>
                  <Form.Td>{activity.description}</Form.Td>
                </Form.Tr>

                <Form.Tr>
                  <Form.Td>Scope</Form.Td>
                  <Form.Td>{activity.activityScope}</Form.Td>
                </Form.Tr>

                <Form.Tr>
                  <Form.Td>Start Date</Form.Td>
                  <Form.Td>{activity.startDate}</Form.Td>
                </Form.Tr>

                <Form.Tr>
                  <Form.Td>Scope</Form.Td>
                  <Form.Td>{activity.activityScope}</Form.Td>
                </Form.Tr>
                <Form.Tr>
                  <Form.Td>Status</Form.Td>
                  <Form.Td> {activity.status}</Form.Td>
                </Form.Tr>

                <Form.Tr>
                  <Form.Td>Review</Form.Td>
                  <Form.Td> {activity.review}</Form.Td>
                </Form.Tr>

                <Form.Tr>
                  <Form.Td>Reviewers</Form.Td>
                  <Form.Td> {activity.reviewers}</Form.Td>
                </Form.Tr>

                <Form.Tr>
                  <Form.Td>Drive Link</Form.Td>
                  <Form.DriveLink
                    driveLink={activity.activityLink}
                    drive={drive}
                  />
                </Form.Tr>

                <Form.Text>
                  <CommentsView
                    reviewee={userLocal.uid}
                    docID={activity.id}
                    docType="activity"
                  />
                </Form.Text>

                <Form.Text>
                  <ActivityUpdate
                    createdBy={userLocal.uid}
                    docID={activity.id}
                    docObject={activity}
                  />
                </Form.Text>
              </Form.Tr>
            ))}
          </Form.Tbody>
        </Form.Table>
        <Form.Link to="/activity">Add Activity</Form.Link>
        <Form.Link to="/activityreview">Review Shared Activities</Form.Link>
        <Form.Link to="/browse">Return to Browse</Form.Link>
        <Form.TextSmall>You are so awesome!</Form.TextSmall>
      </Form>
    </>
  );
}
