import React, { useState, useContext } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//SK
import { db } from '../context/firebase';

export default function AwardUpdate(props) {
  const { firebase } = useContext(FirebaseContext);

  const [error] = useState('');
  //SK
  var user = firebase.auth().currentUser;
  const [setLoader] = useState(false);

  const [awardType, setAwardType] = useState(props.docObject.awardType);
  const [awardName, setAwardName] = useState(props.docObject.awardName);

  const [awardScope, setAwardScope] = useState(props.docObject.awardScope);
  const [description, setDescription] = useState(props.docObject.description);

  const [usage] = useState(props.docObject.usage);
  const [updates] = useState(props.docObject.updates + 1);

  const [years, setYears] = useState(props.docObject.years);
  const [niche, setNiche] = useState(props.docObject.niche);

  const [review, setReview] = useState(props.docObject.review);

  const [awardDate, setAwardDate] = useState(props.docObject.awardDate);
  const [awardLink, setAwardLink] = useState(props.docObject.awardLink);

  const [deleteDocument, setDeleteDocument] = useState(
    props.docObject.deleteDocument
  );

  const ShowDeleteAward = () => {
    const [showText, setShowText] = useState(false);

    return (
      <div>
        {!deleteDocument && (
          <button
            onClick={() => {
              setShowText(!showText);
              console.log('showText: ' + showText);
              console.log('deleteDocument: ' + deleteDocument);
            }}
          >
            Delete
          </button>
        )}

        {showText && (
          <label>
            <Form.Text>Confirm Delete Award</Form.Text>
            <Form.Input
              placeholder="Confirm Delete Award"
              type="checkbox"
              checked={deleteDocument}
              onChange={({ target }) => setDeleteDocument(target.value)}
            />
          </label>
        )}
        {deleteDocument && (
          <Form.Text>
            Please press [Save Award] button to complete the Delete!
          </Form.Text>
        )}
      </div>
    );
  };

  const isInvalid = awardName === '';

  const handleAwardUpdate = (event) => {
    event.preventDefault();

    db.collection('award')
      .doc(props.docID)
      .set(
        {
          awardType: awardType,
          awardName: awardName,
          awardScope: awardScope,
          description: description,
          usage: usage,
          updates: updates,
          years: years,
          niche: niche,
          review: review,
          awardDate: awardDate,
          awardLink: awardLink,
          updatedAt: new Date(),
          updatedBy: user.uid,
          deleteDocument: deleteDocument,
          users: [
            {
              userId: user.uid,
            },
          ],
        },
        { merge: true }
      )
      .then(() => {
        setLoader(false);
        // alert('Your award has been updated!');
      })
      .catch((error) => {
        alert(error.message);
        setLoader(false);
      });
  };

  return (
    <>
      <Form>
        <Form.SubTitle>Update Award</Form.SubTitle>
        {error && <Form.Error>{error}</Form.Error>}

        <Form.Base onSubmit={handleAwardUpdate} method="POST">
          <Form.Input
            placeholder="award Type"
            data-lpignore="true"
            value={awardType}
            onChange={({ target }) => setAwardType(target.value)}
          />

          <Form.Input
            placeholder="award Name"
            data-lpignore="true"
            value={awardName}
            onChange={({ target }) => setAwardName(target.value)}
          />

          <label>
            <Form.Text>Choose Scope:</Form.Text>
            <select
              value={awardScope}
              onChange={({ target }) => setAwardScope(target.value)}
            >
              {' '}
              <option value="Regional">Regional</option>
              <option value="National">National</option>
              <option value="Global">Global</option>
              <option value="None">None</option>
            </select>
          </label>

          <Form.Input
            placeholder="Description"
            data-lpignore="true"
            value={description}
            onChange={({ target }) => setDescription(target.value)}
          />
          <Form.Input
            placeholder="Award Date"
            type="date"
            value={awardDate}
            onChange={({ target }) => setAwardDate(target.value)}
          />

          <Form.Input
            placeholder="Years"
            data-lpignore="true"
            value={years}
            onChange={({ target }) => setYears(target.value)}
          />

          <Form.Input
            placeholder="Niche"
            data-lpignore="true"
            value={niche}
            onChange={({ target }) => setNiche(target.value)}
          />

          <label>
            <Form.Text>Choose Review:</Form.Text>
            <select
              value={review}
              onChange={({ target }) => setReview(target.value)}
            >
              {' '}
              <option value="Draft">Draft</option>
              <option value="Review">Review</option>
              <option value="Final">Final</option>
              <option value="None">None</option>
            </select>
          </label>

          <Form.Input
            placeholder="Award Link"
            data-lpignore="true"
            value={awardLink}
            onChange={({ target }) => setAwardLink(target.value)}
          />

          <ShowDeleteAward />

          <Form.Submit
            disabled={isInvalid}
            type="submit"
            data-testid="award-update"
          >
            Save Award
          </Form.Submit>
        </Form.Base>

        <Form.Text>
          Want to see awards? <Form.Link to="/awardview">View Awards</Form.Link>
        </Form.Text>
        <Form.Link to="/menu-stories-view">Return to Stories</Form.Link>
        <Form.Link to="/browse">Return to Browse</Form.Link>
        <Form.TextSmall>You are so awardable!</Form.TextSmall>
      </Form>
    </>
  );
}
