import React, { useState, useContext } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//SK
import { db } from '../context/firebase';

export default function HonorUpdate(props) {
  const { firebase } = useContext(FirebaseContext);

  const [error] = useState('');
  //SK
  var user = firebase.auth().currentUser;
  const [setLoader] = useState(false);

  const [honorType, setHonorType] = useState(props.docObject.honorType);
  const [honorName, setHonorName] = useState(props.docObject.honorName);

  const [honorScope, setHonorScope] = useState(props.docObject.honorScope);
  const [description, setDescription] = useState(props.docObject.description);

  const [usage] = useState(props.docObject.usage);
  const [updates] = useState(props.docObject.updates + 1);

  const [years, setYears] = useState(props.docObject.years);
  const [niche, setNiche] = useState(props.docObject.niche);

  const [review, setReview] = useState(props.docObject.review);

  const [honorDate, setHonorDate] = useState(props.docObject.honorDate);
  const [honorLink, setHonorLink] = useState(props.docObject.honorLink);

  const [deleteDocument, setDeleteDocument] = useState(
    props.docObject.deleteDocument
  );

  const ShowDeleteHonor = () => {
    const [showText, setShowText] = useState(false);

    return (
      <div>
        {!deleteDocument && (
          <button
            onClick={() => {
              setShowText(!showText);
              console.log('showText: ' + showText);
              console.log('deleteDocument: ' + deleteDocument);
            }}
          >
            Delete
          </button>
        )}

        {showText && (
          <label>
            <Form.Text>Confirm Delete Honor</Form.Text>
            <Form.Input
              placeholder="Confirm Delete Honor"
              type="checkbox"
              checked={deleteDocument}
              onChange={({ target }) => setDeleteDocument(target.value)}
            />
          </label>
        )}
        {deleteDocument && (
          <Form.Text>
            Please press [Save Honor] button to complete the Delete!
          </Form.Text>
        )}
      </div>
    );
  };

  const isInvalid = honorName === '';

  const handlehonorUpdate = (event) => {
    event.preventDefault();

    db.collection('honor')
      .doc(props.docID)
      .set(
        {
          honorType: honorType,
          honorName: honorName,
          honorScope: honorScope,
          description: description,
          usage: usage,
          updates: updates,
          years: years,
          niche: niche,
          review: review,
          honorDate: honorDate,
          honorLink: honorLink,
          updatedAt: new Date(),
          updatedBy: user.uid,
          deleteDocument: deleteDocument,
          users: [
            {
              userId: user.uid,
            },
          ],
        },
        { merge: true }
      )
      .then(() => {
        setLoader(false);
        // alert('Your honor has been updated!');
      })
      .catch((error) => {
        alert(error.message);
        setLoader(false);
      });
  };

  return (
    <>
      <Form>
        <Form.SubTitle>Update Honor</Form.SubTitle>
        {error && <Form.Error>{error}</Form.Error>}

        <Form.Base onSubmit={handlehonorUpdate} method="POST">
          <Form.Input
            placeholder="honor Type"
            data-lpignore="true"
            value={honorType}
            onChange={({ target }) => setHonorType(target.value)}
          />

          <Form.Input
            placeholder="honor Name"
            data-lpignore="true"
            value={honorName}
            onChange={({ target }) => setHonorName(target.value)}
          />

          <label>
            <Form.Text>Choose Scope:</Form.Text>
            <select
              value={honorScope}
              onChange={({ target }) => setHonorScope(target.value)}
            >
              {' '}
              <option value="Regional">Regional</option>
              <option value="National">National</option>
              <option value="Global">Global</option>
              <option value="None">None</option>
            </select>
          </label>

          <Form.Input
            placeholder="Description"
            data-lpignore="true"
            value={description}
            onChange={({ target }) => setDescription(target.value)}
          />
          <Form.Input
            placeholder="honor Date"
            type="date"
            value={honorDate}
            onChange={({ target }) => setHonorDate(target.value)}
          />

          <Form.Input
            placeholder="Years"
            data-lpignore="true"
            value={years}
            onChange={({ target }) => setYears(target.value)}
          />

          <Form.Input
            placeholder="Niche"
            data-lpignore="true"
            value={niche}
            onChange={({ target }) => setNiche(target.value)}
          />

          <label>
            <Form.Text>Choose Review:</Form.Text>
            <select
              value={review}
              onChange={({ target }) => setReview(target.value)}
            >
              {' '}
              <option value="Draft">Draft</option>
              <option value="Review">Review</option>
              <option value="Final">Final</option>
              <option value="None">None</option>
            </select>
          </label>

          <Form.Input
            placeholder="honor Link"
            data-lpignore="true"
            value={honorLink}
            onChange={({ target }) => setHonorLink(target.value)}
          />

          <ShowDeleteHonor />

          <Form.Submit
            disabled={isInvalid}
            type="submit"
            data-testid="honor-update"
          >
            Save Honor
          </Form.Submit>
        </Form.Base>

        <Form.Text>
          Want to see honors? <Form.Link to="/honorview">View Honors</Form.Link>
        </Form.Text>
        <Form.Link to="/menu-stories-view">Return to Stories</Form.Link>
        <Form.Link to="/browse">Return to Browse</Form.Link>
        <Form.TextSmall>You are so honorable!</Form.TextSmall>
      </Form>
    </>
  );
}
