import React, { useState, useContext, useEffect } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//SK
import { db } from '../context/firebase';

export default function ParentViewSummary() {
  const { firebase } = useContext(FirebaseContext);

  const [error, setError] = useState('');

  const [parents, setParents] = useState([]);
  // SK
  const [userLocal, setUserLocal] = useState(
    JSON.parse(localStorage.getItem('authUser'))
  );

  useEffect(() => {
    db.collection('parent')
      .where('uid', '==', userLocal.uid)
      .get()
      .then((response) => {
        const fetchedParents = [];
        response.docs.forEach((document) => {
          const fetchedParent = {
            id: document.id,
            ...document.data(),
          };
          fetchedParents.push(fetchedParent);
        });
        setParents(fetchedParents);
      })
      .catch((error) => {
        setError(error);
      });
  }, []);

  return (
    <>
      <Form.SubTitle>View Parent</Form.SubTitle>
      {error && <Form.Error>{error}</Form.Error>}
      <Form.Table>
        <Form.Thead>
          <Form.Tr>
            <Form.Th>Type</Form.Th>
            <Form.Th>Name</Form.Th>
            <Form.Th>Birthplace</Form.Th>

            <Form.Th>Email</Form.Th>
            <Form.Th>Phone</Form.Th>

            <Form.Th>Occupation</Form.Th>
            <Form.Th>Education</Form.Th>

            <Form.Th>Description</Form.Th>
            <Form.Th>Usage</Form.Th>

            <Form.Th>Date</Form.Th>
          </Form.Tr>
        </Form.Thead>
        <Form.Tbody>
          {error ? <p>Ops, there is an error :(</p> : null}
          {parents.map((parent) => (
            <Form.Tr key={parent.id}>
              <Form.Td> {parent.parentType}</Form.Td>
              <Form.Td> {parent.parentName}</Form.Td>
              <Form.Td> {parent.parentBirthplace}</Form.Td>

              <Form.Td> {parent.email}</Form.Td>
              <Form.Td> {parent.phone}</Form.Td>

              <Form.Td> {parent.occupation}</Form.Td>
              <Form.Td> {parent.education}</Form.Td>

              <Form.Td> {parent.description}</Form.Td>
              <Form.Td> {parent.address}</Form.Td>

              <Form.Td> {parent.parentDate}</Form.Td>
            </Form.Tr>
          ))}
        </Form.Tbody>
      </Form.Table>
    </>
  );
}
