import React, { useState, useContext } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//SK
import { db } from '../context/firebase';

export default function Niche() {
  const { firebase } = useContext(FirebaseContext);

  const [error] = useState('');
  //SK
  var user = firebase.auth().currentUser;
  const [setLoader] = useState(false);

  const [nicheType, setNicheType] = useState('');
  const [nicheName, setNicheName] = useState('');
  const [description, setDescription] = useState('');
  const [usage, setUsage] = useState(0);
  const [updates, setUpdates] = useState(0);

  const [nicheDate, setNicheDate] = useState('');
  const [nicheLink, setNicheLink] = useState('');

  const [deleteDocument, setDeleteDocument] = useState(false);


  const isInvalid = nicheName === '';

  const handleNiche = (event) => {
    event.preventDefault();

    db.collection('niche')
      .add({
        nicheType: nicheType,
        nicheName: nicheName,
        description: description,
        usage: usage,
        updates: updates,
        nicheDate: nicheDate,
        nicheLink: nicheLink,
        createdAt: new Date(),
        createdBy: user.uid,
        deleteDocument: deleteDocument,
        users: [
          {
            userId: user.uid,
          },
        ],
      })
      .then(() => {
        setLoader(false);
        alert('Your niche has been saved!');
      })
      .catch((error) => {
        alert(error.message);
        setLoader(false);
      });
    setNicheType('');
    setNicheName('');

    setDescription('');
    setUsage(0);
    setUpdates(0);

    setNicheDate('');
    setNicheLink('');
    setDeleteDocument(false);

  };

  return (
    <>
      <Form>
        <Form.Title>Add Niche</Form.Title>
        {error && <Form.Error>{error}</Form.Error>}

        <Form.Base onSubmit={handleNiche} method="POST">
          <Form.Input
            placeholder="Niche Type"
            data-lpignore="true"
            value={nicheType}
            onChange={({ target }) => setNicheType(target.value)}
          />

          <Form.Input
            placeholder="Niche Name"
            data-lpignore="true"
            value={nicheName}
            onChange={({ target }) => setNicheName(target.value)}
          />

          <Form.Input
            placeholder="Description"
            data-lpignore="true"
            value={description}
            onChange={({ target }) => setDescription(target.value)}
          />
          <Form.Input
            placeholder="Niche Date"
            type="date"
            value={nicheDate}
            onChange={({ target }) => setNicheDate(target.value)}
          />
          <Form.Input
            placeholder="Niche Link"
            data-lpignore="true"
            value={nicheLink}
            onChange={({ target }) => setNicheLink(target.value)}
          />
          <Form.Submit disabled={isInvalid} type="submit" data-testid="add-niche">
            Save Niche
          </Form.Submit>
        </Form.Base>

        <Form.Text>
          Want to see Niches? <Form.Link to="/nicheview">View Niches</Form.Link>
        </Form.Text>
        <Form.Link to="/menu-stories-view">Return to Stories</Form.Link>
        <Form.Link to="/browse">Return to Browse</Form.Link>
        <Form.TextSmall>You are so awesome!</Form.TextSmall>
      </Form>
    </>
  );
}
