import React, { useState, useContext, useEffect } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//SK
import { db } from '../context/firebase';

export default function EssayViewSummary() {
  const { firebase } = useContext(FirebaseContext);

  const [error, setError] = useState('');
  const [essays, setEssays] = useState([]);
  const [userLocal, setUserLocal] = useState(
    JSON.parse(localStorage.getItem('authUser'))
  );

  useEffect(() => {
    db.collection('essay')
      .where('createdBy', '==', userLocal.uid)
      .get()
      .then((response) => {
        const fetchedEssays = [];
        response.docs.forEach((document) => {
          const fetchedEssay = {
            id: document.id,
            ...document.data(),
          };
          fetchedEssays.push(fetchedEssay);
        });
        setEssays(fetchedEssays);
      })
      .catch((error) => {
        setError(error);
      });
  }, []);

  return (
    <>
      <Form.SubTitle>View Essays</Form.SubTitle>
      {error && <Form.Error>{error}</Form.Error>}

      <Form.Table>
        <Form.Thead>
          <Form.Tr>
            <Form.Th>Type</Form.Th>
            <Form.Th>Name</Form.Th>
            <Form.Th>Description</Form.Th>
            <Form.Th>Length</Form.Th>

            <Form.Th>Date</Form.Th>

            <Form.Th>Status</Form.Th>
            <Form.Th>Review</Form.Th>
          </Form.Tr>
        </Form.Thead>

        <Form.Tbody>
          {error ? <p>Ops, there is an error :(</p> : null}
          {essays.map((essay) => (
            <Form.Tr key={essay.id}>
              <Form.Td> {essay.essayType}</Form.Td>

              <Form.Td> {essay.essayName}</Form.Td>

              <Form.Td> {essay.description}</Form.Td>
              <Form.Td> {essay.length}</Form.Td>

              <Form.Td> {essay.essayDate}</Form.Td>

              <Form.Td> {essay.status}</Form.Td>
              <Form.Td> {essay.review}</Form.Td>
            </Form.Tr>
          ))}
        </Form.Tbody>
      </Form.Table>
    </>
  );
}
