import React, { useState, useContext } from 'react';
import { FirebaseContext } from '../context/firebase';
import { Form } from '../components';

//SK
import { db } from '../context/firebase';

export default function Essay() {
  const { firebase } = useContext(FirebaseContext);

  const [error] = useState('');
  //SK
  var user = firebase.auth().currentUser;
  const [ setLoader] = useState(false);

  const [essayType, setEssayType] = useState('');
  const [essayPrompt, setEssayPrompt] = useState('');

  const [essayName, setEssayName] = useState('');
  const [description, setDescription] = useState('');
  const [length, setLength] = useState(0);

  const [essayDate, setEssayDate] = useState('');

  const [status, setStatus] = useState('None');
  const [review, setReview] = useState('None');

  const [essayLink, setEssayLink] = useState('');

  const [deleteDocument, setDeleteDocument] = useState(false);

  const isInvalid = essayName === '';

  const handleEssay = (event) => {
    event.preventDefault();

    //        createdAt: firebase.firestore.FieldValue.serverTimestamp(),

    db.collection('essay')
      .add({
        essayType: essayType,
        essayPrompt: essayPrompt,

        essayName: essayName,
        description: description,
        length: length,
        essayDate: essayDate,

        status: status,
        review: review,

        essayLink: essayLink,

        createdAt: new Date(),
        createdBy: user.uid,
        deleteDocument: deleteDocument,
        users: [
          {
            userId: user.uid,
          },
        ],
      })
      .then(() => {
        setLoader(false);
        alert('Your essay has been saved!');
      })
      .catch((error) => {
        alert(error.message);
        setLoader(false);
      });
    setEssayType('');
    setEssayPrompt('');

    setEssayName('');
    setDescription('');

    setStatus('None');
    setReview('None');

    setEssayDate('');
    setEssayLink('');

    setDeleteDocument(false);
  };

  return (
    <>
      <Form>
        <Form.Title>Add Essay</Form.Title>
        {error && <Form.Error>{error}</Form.Error>}
        <Form.Base onSubmit={handleEssay} method="POST">
          <Form.Input
            placeholder="Essay Type"
            data-lpignore="true"
            value={essayType}
            onChange={({ target }) => setEssayType(target.value)}
          />

          <Form.Table>
            <Form.Tr>
              <Form.Td>
                <label>
                  <Form.Text>Choose Prompt:</Form.Text>
                  <select
                    value={essayPrompt}
                    onChange={({ target }) => setEssayPrompt(target.value)}
                  >
                    {' '}
                    <option value="Hardship">Hardship I overcame</option>
                    <option value="Background">Background Story</option>
                    <option value="Belief">Belief or Idea I challenged</option>
                    <option value="Accomplishment">
                      Accomplishment with Growth
                    </option>
                    <option value="Idea">Engaging Idea</option>
                    <option value="Personal">Personal Choice</option>
                  </select>
                </label>
              </Form.Td>
            </Form.Tr>
          </Form.Table>

          <Form.Input
            placeholder="Essay Name"
            value={essayName}
            data-lpignore="true"
            onChange={({ target }) => setEssayName(target.value)}
          />

          <Form.InputLong
            placeholder="Description"
            value={description}
            data-lpignore="true"
            onChange={({ target }) => {
              setDescription(target.value);
              setLength(target.value.length);
            }}
          />

          <Form.Input
            placeholder="Essay Date"
            type="date"
            value={essayDate}
            data-lpignore="true"
            onChange={({ target }) => setEssayDate(target.value)}
          />

          <Form.Table>
            <Form.Tr>
              <Form.Td>
                <label>
                  <Form.Text>Choose Status:</Form.Text>
                  <select
                    value={status}
                    onChange={({ target }) => setStatus(target.value)}
                  >
                    {' '}
                    <option value="Current">Current</option>
                    <option value="Planned">Planned</option>
                    <option value="Completed">Completed</option>
                    <option value="None">None</option>
                  </select>
                </label>
              </Form.Td>
              <Form.Td>
                <label>
                  <Form.Text>Choose Review:</Form.Text>
                  <select
                    value={review}
                    onChange={({ target }) => setReview(target.value)}
                  >
                    {' '}
                    <option value="Draft">Draft</option>
                    <option value="Review">Review</option>
                    <option value="Final">Final</option>
                    <option value="None">None</option>
                  </select>
                </label>
              </Form.Td>
            </Form.Tr>
          </Form.Table>

          <Form.Input
            placeholder="Essay Link"
            type="url"
            value={essayLink}
            data-lpignore="true"
            onChange={({ target }) => setEssayLink(target.value)}
          />

          <Form.Submit disabled={isInvalid} type="submit" data-testid="sign-up">
            Save Essay
          </Form.Submit>
        </Form.Base>
        <Form.Text>
          Want to see Essays? <Form.Link to="/essayview">View Essays</Form.Link>
        </Form.Text>
        <Form.Link to="/browse">Return to Browse</Form.Link>
        <Form.TextSmall>You are so awesome!</Form.TextSmall>
      </Form>
    </>
  );
}
